import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";
import { BaseConfigContainer, ConfigData } from "./BaseConfigContainer";
import { decodeConfigItemString } from "./ItemConfigContainer";
import { ModuleDef } from "db://assets/scripts/ModuleDef";
import { JsonAsset } from "cc";
import { Logger } from "db://assets/core_tgx/base/log/Logger";

export class CoinMatchData {
    coin_match_id;
    coin_match_name;
    clearing_type;
    base_coin;
    limit_enter_coin;
    commission;
    stage_integral_win;
    stage_integral_lose;
    win_extra_reward;
    bigwin_reward;
}

const enum CoinMatchTitle {
    coin_match_id,
    coin_match_name,
    clearing_type,
    base_coin,
    limit_enter_coin,
    commission,
    stage_integral_win,
    stage_integral_lose,
    win_extra_reward,
    bigwin_reward
}

export class CoinMatchConfigContainer extends BaseConfigContainer {
    private configData: ConfigData = null;
    private mapConfig: any = null;

    constructor(callback: Function, caller: any, arg: any) {
        super();
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN,this.CONFIG_FILE_DIR + "coin_match",JsonAsset,(err: Error, jsonAsset: JsonAsset) => {
            if (err) {
                Logger.error("load coin_match err");
            }
            else {
                this.configData = jsonAsset.json as ConfigData;

                this.mapConfig = {};
                for (let i = 0; i < this.configData.RECORDS.length; i++) {
                    let record = this.configData.RECORDS[i];
                    let data = new CoinMatchData();
                    data.coin_match_id = record[CoinMatchTitle.coin_match_id];
                    data.coin_match_name = record[CoinMatchTitle.coin_match_name];
                    data.clearing_type = record[CoinMatchTitle.clearing_type];
                    data.base_coin = record[CoinMatchTitle.base_coin];
                    data.limit_enter_coin = record[CoinMatchTitle.limit_enter_coin];
                    data.commission = record[CoinMatchTitle.commission];
                    data.stage_integral_win = decodeConfigItemString(record[CoinMatchTitle.stage_integral_win]);
                    data.win_extra_reward = decodeConfigItemString(record[CoinMatchTitle.win_extra_reward]);
                    data.bigwin_reward = decodeConfigItemString(record[CoinMatchTitle.bigwin_reward]);
                    this.mapConfig[data.coin_match_id] = data;
                }
                if (callback) {
                    callback.call(caller, arg);
                }
            }
        });
    }

    /**
     * 
     * @param coin_match_id 
     */
    public getCoinMatchCfgByCoinMatchId(coin_match_id: string): CoinMatchData {
        return this.mapConfig[coin_match_id];
    }

    public getCoinItemsByClearingType(clearing_type): CoinMatchData[] {
        let arr = [];
        for (const key in this.mapConfig) {
            let element = this.mapConfig[key] as CoinMatchData;
            if (element.clearing_type == clearing_type) {
                arr.push(element);
            }
        }
        return arr;
    }
}