import { _decorator, Component, Animation, Node, Prefab, Label } from 'cc';
const { ccclass, property } = _decorator;

import PDK_FlyGold from "./PDK_FlyGold";
import PDK_PlayerSoundHelper from "./PDK_PlayerSoundHelper";
import { dragonBones } from 'cc';
import GameTools from '../../kernel/GameTools';
import { instantiate } from 'cc';
import Utils from '../../kernel/Utils';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { Tween } from 'cc';
import { easing } from 'cc';
import { animation } from 'cc';
import { AnimationClip } from 'cc';

@ccclass('PDKHeadframeAni')
export default class PDK_HeadframeAni extends Component {
    @property(Animation)
    ani: Animation | null = null;
    @property(Node)
    spWin: Node | null = null;
    @property(Node)
    spLose: Node | null = null;
    @property(dragonBones.ArmatureDisplay)
    goldFall: dragonBones.ArmatureDisplay = null;
    @property(Prefab)
    flyGoldPrefab: Prefab | null = null;
    @property(Node)
    battleScoreNode: Node | null = null;
    @property(Node)
    seatRoot: Node | null = null;
    @property(Node)
    seat0: Node | null = null;
    @property(Node)
    seat1: Node | null = null;
    @property(Node)
    seat2: Node | null = null;
    @property(Node)
    seat3: Node | null = null;
    private m_lblPosScore: Label | null = null;
    private m_lblNegScore: Label | null = null;
    private m_seatIndex: number;
    private m_passAni: Animation | null = null;
    onLoad() {

        this.m_passAni = this.node.getChildByName("pass_ani").getComponent(Animation);
        let parentName = this.node.parent.name;
        this.m_seatIndex = Number(parentName.replace("seat_", ""));
        this.m_lblPosScore = this.battleScoreNode.getChildByName("pos").getComponent(Label);
        this.m_lblPosScore.node.active = false;
        this.m_lblNegScore = this.battleScoreNode.getChildByName("neg").getComponent(Label);
        this.m_lblNegScore.node.active = false;
        this.reset();
    }
    onDestroy() {

    }
    reset() {
        this.ani.node.active = false;
        this.spWin.active = false;
        this.spLose.active = false;
        if (this.m_passAni != null) {
            this.m_passAni.node.active = false;
        }
    }
    showWin(score: number) {
        this.scheduleOnce(() => {
            this.doShowWin(score);
        }, 1);
    }
    doShowWin(score) {
        this.ani.node.active = true;
        this.spWin.active = true;
        this.spLose.active = false;
        this.ani.play("win");
        this.scheduleOnce(() => {
        this.showGoldFall();
        }, 0.5);
        this.scheduleOnce(() => {
        this.showScoreAni(score);
        }, 1.5)
    }
    public showGoldFall() {
        this.goldFall.playAnimation("newAnimation", 1);
        let soundUrl: string = "sound/coin2";
        PDK_PlayerSoundHelper.playSoundUrl(soundUrl);
    }
    showLose(score: number) {
        this.ani.node.active = true;
        this.spWin.active = false;
        this.spLose.active = true;
        this.ani.play("win");
        this.scheduleOnce(() => {
            this.showScoreAni(score);
        }, 1.5);
    }
    showScoreAni(score:number) {
        let lbl: Label = null;
        if (score > 0) {
            this.m_lblNegScore.node.active = false;
            this.m_lblPosScore.node.active = true;
            this.m_lblPosScore.string = "+" + GameTools.nFormatter(score);
            lbl = this.m_lblPosScore;
        }
        else {
            score = -score;
            this.m_lblNegScore.node.active = true;
            this.m_lblPosScore.node.active = false;
            this.m_lblNegScore.string = "-" + GameTools.nFormatter(score);
            lbl = this.m_lblNegScore;
        }
        if (lbl) {
            lbl.node.opacity = 0;
            lbl.node.setScale(new Vec3(0.8, 0.8, 1));

            //  tween 
            Tween.stopAllByTarget(lbl.node);

            // 
            const fadeInTween = tween(lbl.node)
               .to(0.5, { opacity: 255 });

            // 
            const scaleTween = tween(lbl.node)
               .to(2, { scale: new Vec3(1, 1, 1) }, { easing: easing.backOut });

            // 
            tween(lbl.node)
               .parallel(fadeInTween, scaleTween)
               .start();
        }
    }
    hideScoreAni(delay: number = 0) {
        this.scheduleOnce( () => {
            if (this.m_lblNegScore.node.isValid) {
                this.m_lblNegScore.node.active = false;
                this.m_lblPosScore.node.active = false;
            }
        }, delay);
    }
    showFlyGold(winSeatIndex: number) {
        if (winSeatIndex == this.m_seatIndex) return;
            let goldCount: number = Utils.randomRangeInt(20, 30);
            let winSeat: Node = this['seat' + winSeatIndex];
            let selfSeat: Node = this['seat' + this.m_seatIndex];
            if (winSeat == null || selfSeat == null) return;
            for (let i = 0; i < goldCount; i++) {
            let gold: Node = instantiate(this.flyGoldPrefab);
            gold.setParent(this.seatRoot);
            let goldFly: PDK_FlyGold = gold.getComponent(PDK_FlyGold);
            goldFly.init(selfSeat.x, selfSeat.y, winSeat.x, winSeat.y);
        }
    }
    showPass(isPass: boolean) {
        if (!this.m_passAni) return;

        if (isPass) {
            this.m_passAni.node.active = true;
            this.m_passAni.defaultClip.wrapMode = AnimationClip.WrapMode.Loop;
            this.m_passAni.play();

            //  tween 
            Tween.stopAllByTarget(this.m_passAni.node);
            this.m_passAni.node.opacity = 0;

            // 
            tween(this.m_passAni.node)
               .to(0.3, { opacity: 255 })
               .start();
        } else {
            if (this.m_passAni.node.active) {
                //  tween 
                Tween.stopAllByTarget(this.m_passAni.node);

                // 
                tween(this.m_passAni.node)
                   .to(0.3, { opacity: 0 })
                   .call(() => {
                        this.m_passAni.stop();
                        this.m_passAni.node.active = false;
                    })
                   .start();
            }
        }
    }
}