        // let exchangeData = exchangeDataList[i];
        // let priceInfo: ItemInfo = exchangeData.price_item;
        // let priceItem = KernelData.pack[priceInfo.itemId];
        // if (priceItem != null) {
        // nowCnt = priceItem.cnt;
        // }
