import { _decorator, Component, Node } from 'cc';
import { GameMgr } from 'db://assets/module_basic/scripts/GameMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { ConfigManager } from './script/ConfigManager';
import { ResourceMgr } from '../core_tgx/base/ResourceMgr';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { GameTool } from './kernel/GD';
import { PokerManager } from '../module_basic/scripts/poker/PokerManager';
import { GameManager } from './script/message/GameManager';
import { assetManager } from 'cc';
import { sp } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PdkYuNanGameMgr')
export class PdkYuNanGameMgr extends GameMgr {
    private static _inst: PdkYuNanGameMgr;
    private _preloadResIndex:number = 0;
    private _preloadResTotal:number;
    
    public static get inst(): PdkYuNanGameMgr {
        if (!this._inst) {
            this._inst = new PdkYuNanGameMgr();
        }
        return this._inst;
    }

    constructor() {
        super();
        this._gameType = SubGameDef.POKER_PDK_YUENAN;
        this._entryScene = { name: 'lobby_pdk_yuenan', bundle: ModuleDef.POKER_PDK_YUENAN };
        this._gameScene = { name: 'game_pdk_yuenan', bundle: ModuleDef.POKER_PDK_YUENAN };
        this._loadingScene = { name: 'subgame_loading', bundle: ModuleDef.BASIC };
        
    }
    override init() {
        GameManager.inst.init();
        PokerManager.inst.init();
        
    }

    override uinit() {
        GameTool.destroyWaitLayer();
        PokerManager.inst.uinit();
        GameManager.inst.uinit();
    }
    /**
     * 
     */
    public override async preLoadRes(processFun:Function):Promise<boolean> {
        this._preloadResIndex = 0;
        this._preloadResTotal = 9;
        processFun(0);
        await this.loadResource(processFun);
        await this.preLoadLobbyScene(processFun);
        await this.preLoadGameScene(processFun);
        processFun(1);
        return true;
    }

    public async loadResource(processFun:Function)
    {
        return new Promise((resolve, reject) => {
                    function loadConfigComplete() {
                        var loadPrefabCount:number = 6;
                        var self = this;
                        function loadPrefabComplete() {
                            
                            loadPrefabCount--;
                            self._preloadResIndex++;
                            processFun(self._preloadResIndex/self._preloadResTotal);
                            if(loadPrefabCount == 0)
                            {
                                resolve(true);
                            }
                        }
                        ResourceMgr.inst.preloadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN, 'kernel/PublicPrefab/UITips/UITips',Prefab,loadPrefabComplete);
                        ResourceMgr.inst.preloadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN, 'kernel/PublicPrefab/UILoading/UILoading',Prefab,loadPrefabComplete);
                        ResourceMgr.inst.preloadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN, 'kernel/PublicPrefab/UIDialogBox/UIMessageBox',Prefab,loadPrefabComplete);


                        ResourceMgr.inst.preloadResByModuleBundle(ModuleDef.BASIC, 'res/room/chuji/ChuJi',sp.SkeletonData,loadPrefabComplete);
                        ResourceMgr.inst.preloadResByModuleBundle(ModuleDef.BASIC, 'res/room/zhongji/zhongji',sp.SkeletonData,loadPrefabComplete);
                        ResourceMgr.inst.preloadResByModuleBundle(ModuleDef.BASIC, 'res/room/gaoji/gaoji',sp.SkeletonData,loadPrefabComplete);
                    }
                    ConfigManager.getInstance().loadAllConfig(loadConfigComplete.bind(this));
                    
                });
       
    }
    private async preLoadLobbyScene(processFun:Function) {
        return new Promise((resolve, reject) => {
            let bundle = assetManager.getBundle(ModuleDef.POKER_PDK_YUENAN);
            if(bundle)
            {
                bundle.preloadScene(SubGameMgr.gameMgr.entryScene.name,null,function ()
                {
                    this._preloadResIndex++;
                    processFun(this._preloadResIndex/this._preloadResTotal);
                    resolve(true);
                }.bind(this));  //7
                
            }
            else
            {
                resolve(false);
            }
        });
        
    }
    private async preLoadGameScene(processFun:Function) {
        return new Promise((resolve, reject) => {
            let bundle = assetManager.getBundle(ModuleDef.FISH);
            if(bundle)
            {
                bundle.preloadScene(SubGameMgr.gameMgr.gameScene.name,null,function ()
                {
                    this._preloadResIndex++;
                    processFun(this._preloadResIndex/this._preloadResTotal);
                    resolve(true);
                }.bind(this));  //7
                
            }
            else
            {
                resolve(false);
            }
        });
        
    }
    // 
    public override async enterGame() {
        SubGameMgr.inst.MatchRoom(this.gameType, true);
    }
}

SubGameMgr.inst.registerGameMgr(PdkYuNanGameMgr.inst);


