import { _decorator, Component } from 'cc';
import IWait from "./IWait";

export default class WaitTime implements IWait {
    public async wait(caller: Component, sec: number): Promise<any> {

        console.log("WaitTime : " + sec);
        if (caller == null || !caller.isValid) return;
        return new Promise((resolve, reject) => {
            caller.scheduleOnce(() => {
                resolve(true);
            }, sec);
        });
    }
}

