
import { BaseConfigContainer, ConfigContainerClass } from "./Conifg/BaseConfigContainer";
import { ItemConfigContainer } from "./Conifg/ItemConfigContainer";
import { LanguageConfigContainer } from "./Conifg/LanguageConfigContainer";
import { CoinMatchConfigContainer } from "./Conifg/CoinMatchConfigContainer";
import { OpenRoomConfigContainer } from "./Conifg/OpenRoomConfigContainer";
import { FaceConfigContainer } from "./Conifg/FaceConfigContainer";
import { DuanjuConfigContainer } from "./Conifg/DuanjuConfigContainer";
import { PeiMusicConfigContainer } from "./Conifg/PeiMusicConfigContainer";
import { GlobalConfigContainer } from "./Conifg/GlobalConfigContainer";

export class ConfigManager
{
    private static instance: ConfigManager;

    private configContainerList: BaseConfigContainer[] = [];
    private curLoadedCount: number = 0;
    private _isLoaded:boolean = false;
    public static getInstance(): ConfigManager
    {
        if(this.instance == null)
        {
            this.instance = new ConfigManager();
        }
        return this.instance;
    }

    public loadAllConfig(callback?: Function): void {
        if(this._isLoaded)
        {
            if(callback)
            {
                callback();
            }
            return;
        }
        this._isLoaded = true;
        this.loadConfig(LanguageConfigContainer, this.callback, callback);
        this.loadConfig(ItemConfigContainer, this.callback, callback);
        this.loadConfig(CoinMatchConfigContainer, this.callback, callback);
        this.loadConfig(OpenRoomConfigContainer, this.callback, callback);
        this.loadConfig(FaceConfigContainer, this.callback, callback);
        this.loadConfig(DuanjuConfigContainer, this.callback, callback);
        this.loadConfig(PeiMusicConfigContainer, this.callback, callback);
        this.loadConfig(GlobalConfigContainer, this.callback, callback);
    }

    public getConfig<T extends BaseConfigContainer>(configClass: ConfigContainerClass<T>): T
    {
        for(let i = 0; i < this.configContainerList.length; ++i)
        {
            if(this.configContainerList[i].tag == configClass)
            {
                return this.configContainerList[i] as T;
            }
        }
        return null;
    }

    public loadConfig<T extends BaseConfigContainer>(configClass: ConfigContainerClass<T>, callback: Function, arg: any)
    {
        let config = new configClass(callback, this, arg);
        config.tag = configClass;
        this.configContainerList.push(config);
    }

    private callback(callback: Function)
    {
        //cc.log("callback");

        this.curLoadedCount += 1;
        if(this.configContainerList.length == this.curLoadedCount)
        {
            if(callback)
            {
                callback();
            }
        }
    }
}