    private showRoundEndScore() {
        console.log("PDK_GameEndPerformanceHelper : showRoundEndScore");
        for (let i = 0; i < this.m_data.userData.length; i++) {
            let oneUserData: UserResultData = this.m_data.userData[i];
            let seatID = PokerUtils.getRelativeSeatID(oneUserData.chairID, PokerManager.inst.myChairID);
            let player: PDK_Player = this.m_game.playerScript[seatID];
            if (player == null || !player.isValid) continue;

            player.showRoundEndScore(oneUserData);
        }
    }
    private hideScoreAni(delay:number) {
        for (let i = 0; i < this.m_data.userData.length; i++) {
            let oneUserData: UserResultData = this.m_data.userData[i];
            let seatID = PokerUtils.getRelativeSeatID(oneUserData.chairID, PokerManager.inst.myChairID);
            let player: PDK_Player = this.m_game.playerScript[seatID];
            if (player == null || !player.isValid) continue;
            player.headAni.hideScoreAni(delay);
        }
    }
    
