    private async showTongpai() {
        if (this.m_game == null || this.m_data == null) return;
        let waitTime: WaitTime = new WaitTime();

        await waitTime.wait(this.m_game, 1);
       // 1
        this.showHandCard();
        await waitTime.wait(this.m_game, 1);
       // 
        this.showGoldFlyAni(waitTime);
        EventCenter.emitEvent(gameExpressionEvent.SHOW_TONGPAI, this.m_isSelfWin);

        await waitTime.wait(this.m_game, 4);
       // 
        this.resetHandCard();
       //  
        await this.showExtraReward(waitTime);
        this.hideScoreAni(1);
    }
