import { _decorator } from 'cc';
import PDK_Card from "../PDK_Card";
import { gameEvent } from "../PDK_define";
import { Singleton } from '../../kernel/Singleton';
import { sys } from 'cc';
import EventCenter from '../../kernel/EventCenter';
import { PokerUtils } from '../message/PokerUtils';

export default class PDK_BattleData extends Singleton {
//    // 
    private readonly local_head_auto_record: string = "local_head_auto_record";
//    // 
    private m_isAutoRecordCards: boolean = undefined;
//    // 
    private m_isUseRecorder: boolean = false;
//    // 
    private m_outCards: number[] = [];
//    // 
    public lastSeat: number = -1;
//    // 
    public lastCardValues: number[] = [];
//    //   false
    public isInBattle: boolean = false;
    public enableCardRecorder: boolean = false;
    public static firstSitDown: boolean = false;
    public extraRewardData: any = null; // {rewardList: string[]}
    constructor() {
        super();
    }
    private clear() {
        PDK_BattleData.firstSitDown = false;

        this.m_isAutoRecordCards = undefined;
        this.m_isUseRecorder = undefined;
        this.m_outCards.length = 0;
        this.lastSeat = -1;
        this.lastCardValues.length = 0;
        this.isInBattle = false;
    }
//    // 
    public setLastChupai(seat: number, cards: number[]) {
        // console.log("set last chupai : " + seat);
        this.lastSeat = seat;
        this.lastCardValues.length = 0;
        for (let i = 0; i < cards.length; i++) {
            let value = PokerUtils.getCardRank(cards[i]);
            this.lastCardValues.push(value);
        }
    }
//    // 
    public get isAutoRecordCards(): boolean {
        if (this.m_isAutoRecordCards == null) {
            let str = sys.localStorage.getItem(this.local_head_auto_record);
            if (str == null) {
                this.m_isAutoRecordCards = true;
                sys.localStorage.setItem(this.local_head_auto_record, true);
            }
            else {
                this.m_isAutoRecordCards = str == "true";
            }
        }
        return this.m_isAutoRecordCards;
    }
//    // 
    public set isAutoRecordCards(value: boolean) {
        this.m_isAutoRecordCards = value;
        sys.localStorage.setItem(this.local_head_auto_record, value);
    }
//    // 
    public recordOutCards(cards: number[]) {
        for (let i = 0; i < cards.length; i++) {
            let card = cards[i];
            if (this.m_outCards.indexOf(card) == -1) {
                this.m_outCards.push(card);
            }
        }
        EventCenter.emitEvent(gameEvent.EVENT_RECORD_OUT_CARD, cards);
    }
//    // 
    public get outCards(): number[] {
        return this.m_outCards;
    }
//    // 
    public set isUseRecorder(value: boolean) {
        this.m_isUseRecorder = value;
        EventCenter.emitEvent(gameEvent.EVENT_USE_RECORDER, value);
    }
    public get isUseRecorder(): boolean {
        return this.m_isUseRecorder;
    }
//    // 
    public onGameStart() {
        this.clear();
        this.extraRewardData = null;
        this.isInBattle = true;
        EventCenter.emitEvent(gameEvent.EVENT_GAME_ROUND_START);
    }
    public onGameFinish() {
        this.clear();
        this.isInBattle = false;
        EventCenter.emitEvent(gameEvent.EVENT_GAME_ROUND_FINISH);
    }
    public onGameExit() {
        this.clear();
        this.extraRewardData = null;
    }
    public checkUseRecorderItem() {
        // if (!this.enableCardRecorder) return;

        // let item = KernelData.pack[1003];
        // if (item != null && item.cnt != 0) {
        // let data = {
        // item_id: 1003,
        // toChairID: KernelData.chairID
        // }
        // clientKernel.use_item(data);
        // }
    }
}