import { _decorator } from 'cc';
import Utils from '../../kernel/Utils';
import { ConfigManager } from '../ConfigManager';
import { PeiMusicConfigContainer } from '../Conifg/PeiMusicConfigContainer';
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { AudioClip } from 'cc';
import { CARD_TYPE } from '../PDK_ConstDef';
import { GameLogic } from 'db://assets/module_fish_whale/script/game/logic/gameLogic';
import PDK_Game from '../PDK_Game';
import PDK_BattleData from '../data/PDK_BattleData';
import PDK_Card from '../PDK_Card';
import { GamePlayCard } from '../message/GameManager';
import { CARD_COLOR } from '../message/Define';
import { PokerUtils } from '../message/PokerUtils';

export default class PDK_PlayerSoundHelper {
    private m_sex: number = -1;
    private m_seat: number = -1;
    public init(seat: number, sex: number) {
        this.m_seat = seat;
        this.m_sex = sex;
    }
    public playCardSound(data: GamePlayCard) {
        let { prevCards, } = data;
        let { cards, userID, leftCardCount, } = prevCards;
        // console.log("play card sound : " + cards);
        var cardType = PDK_Game.ins.GameLogic.getCardType(cards);
        if (cardType == CARD_TYPE.NULL) return; // 
        let lastSeat = PDK_BattleData.instance().lastSeat;
        let lastCardsValue: number[] = PDK_BattleData.instance().lastCardValues;
       // 
        let currentRoundFirst: boolean = lastSeat == -1 || lastSeat == this.m_seat;
        switch (cardType) {
            case CARD_TYPE.SINGLE: {
                let value = PokerUtils.getCardRank(cards[0]);
                this.processSingle(currentRoundFirst, value, lastCardsValue, cards);
                }
                break;

            case CARD_TYPE.PAIR: {
                let value = PokerUtils.getCardRank(cards[0]);
                this.processPair(currentRoundFirst, value);
                }
                break;

            case CARD_TYPE.THREE: {
                // let value = PDK_Card.getCardValue(cards[0]);
                this.processThree(currentRoundFirst);
                }
                break;

            case CARD_TYPE.STRAIGHT: {
                this.processStraight(currentRoundFirst, cards);
                }
                break;

            case CARD_TYPE.PAIRS: {
                this.processPairs(currentRoundFirst, cards);
                }
                break;

            case CARD_TYPE.FOUR: {
                this.processFour(currentRoundFirst);
                }
                break;
        }
    }
//    // 
    private processSingle(currentRoundFirst: boolean, value: number, lastCardsValue: number[], cards: number[]) {
        if (currentRoundFirst) {
           // 
            if (value < 3) {    // 1 2 
                this.playRandom(this.playSingle, value, this.playSoundById, 10080)
            }
            else {
                this.playSoundById(10080);  // 3 - K1
            }
        }
        else {
            // 
            // 
            if (value == lastCardsValue[0]) {
                let soundId: number = 0;
                let color = PDK_Card.getCardColor(cards[0]);
                if (color == CARD_COLOR.Diamond) {
                    soundId = 10120
                }
                else if (color == CARD_COLOR.HEART) {
                    soundId = 10130
                }
                else if (color == CARD_COLOR.CLUB) {
                    soundId = 10110
                }
                else if (color == CARD_COLOR.SPADE) {
                    soundId = 10100
                }

                this.playRandom(this.playSoundById, 10010, this.playSoundById, 10020, this.playSoundById, soundId, this.playSingle, value);
            }
            else {
                this.playRandom(this.playSoundById, 10010, this.playSoundById, 10020, this.playSingle, value);
            }
    }
    }
//    // 
    private processPair(currentRoundFirst: boolean, value: number) {
        if (currentRoundFirst) {
           // 
            this.playRandom(this.playSoundById, 10160, this.playPair, value);
        }
        else {  // 
            this.playRandom(this.playSoundById, 10010, this.playSoundById, 10020, this.playSoundById, 10160, this.playPair, value);
        }
    }
//    // 3
    private processThree(currentRoundFirst: boolean) {
        if (currentRoundFirst) {
           // 
            this.playSoundById(10190);  // 3
        }
        else {
           // 
            this.playRandom(this.playSoundById, 10010, this.playSoundById, 10020, this.playSoundById, 10190);
        }
    }
//    // 
    private processStraight(currentRoundFirst: boolean, cards: number[]) {
        let len = cards.length;
        if (len == 3) {
           // 3
            let value = PokerUtils.getCardRank(cards[0]);
            let soundId:number = 10210 + value - 3;
            if (currentRoundFirst) {
                // 
            this.playRandom(this.playSoundById, 10200, this.playSoundById, soundId);
            }
            else {
                // 
                this.playRandom(this.playSoundById, 10010, this.playSoundById, 10020, this.playSoundById, 10200, this.playSoundById, soundId);
            }
        }
        else {
           //4 
            if (currentRoundFirst) {
                // 
                this.playSoundById(10200);
            }
            else {
                // 
                this.playRandom(this.playSoundById, 10010, this.playSoundById, 10020, this.playSoundById, 10200);
            }
        }
    }
//    // 
    private processPairs(currentRoundFirst: boolean, cards: number[]) {
        if (cards.length == 6) {
           // 3
            if (currentRoundFirst) {
                    // 
                this.playSoundById(10060);
            }
            else {
                    // 
                this.playRandom(this.playSoundById, 10020, this.playSoundById, 10060);
            }
        }
        else if (cards.length == 8) {
            // 4
            if (currentRoundFirst) {
                    // 
                this.playSoundById(10040);
            }
            else {
                    // 
                this.playRandom(this.playSoundById, 10020, this.playSoundById, 10040);
            }
        }
    }
   // 
    private processFour(currentRoundFirst: boolean) {
        if (currentRoundFirst) {
           // 
            this.playSoundById(10050);
        }
        else {
           // 
            this.playRandom(this.playSoundById, 10020, this.playSoundById, 10050);
        }
    }
    private playRandom(fun1: Function, param1: any = null, fun2: Function = undefined, param2: any = null, fun3: Function = undefined, param3: any = null, fun4: Function = undefined, param4: any = null) {
        let max = 0;
        if (fun1 != null) {
            max = 0;
        }
        if (fun2 != null) {
            max = 1;
        }
        if (fun3 != null) {
            max = 2;
        }
        if (fun4 != null) {
            max = 3;
        }

        let idx = Utils.randomRangeInt(0, max);
        if (idx == 0) {
            fun1.call(this, param1);
        }
        else if (idx == 1) {
            fun2.call(this, param2);
        }
        else if (idx == 2) {
            fun3.call(this, param3);
        }
        else if (idx == 3) {
            fun4.call(this, param4);
        }
    }
    private playSingle(value: number) {
        if (value == 1) {
            this.playSoundById(10151);
        }
        else if (value == 2) {
            this.playSoundById(10152);
        }
        else {
            let id: number = 10140 + value - 3;
            this.playSoundById(id);
        }
    }
    private playPair(value: number) {
        if (value == 1) {
            this.playSoundById(10181);
        }
        else if (value == 2) {
            this.playSoundById(10182);
        }
        else {
            let id: number = 10170 + value - 3;
            this.playSoundById(id);
        }
    }
    public playPass() {
        this.playSoundById(10000);
    }
    public playWin() {
        this.playSoundById(10030);
    }
    public playTongpai() {
        this.playSoundById(10070);
    }
    private playSoundById(soundId: number) {
        if(this){
            PDK_PlayerSoundHelper.playerSound(soundId, this.m_sex);
        }
    }
    public static playSoundUrl(url: string) {
        tgx.AudioMgr.inst.audio.playEffect(url,ModuleDef.POKER_PDK_YUENAN);
    }
    
    public static playerSound(soundId: number, sex: number) {
        let peiConfig: PeiMusicConfigContainer = ConfigManager.getInstance().getConfig(PeiMusicConfigContainer);
        let pei = peiConfig.getDataById(soundId);
        if (pei == null)
        {
            console.error("could not find play pei sound id : " + soundId);
            return;
        }
        // console.log("play pei sound : " + soundId);
        let idxMax = pei.arrSound.length - 1;
        let idx = Utils.randomRangeInt(0, idxMax);
        let res: string = pei.arrSound[idx];
        let sexPath: string = sex == 1 ? "boy/" : "girl/";
        let path = "pdk/sound/" + sexPath + res;
        this.playSoundUrl(path);
    }
}