    /**
     * zpF:
     * @param min 
     * @param max 
     * @param isInt 
     * @returns {number} 
     */
    public static randNum(min, max, isInt) {
        min = min || 0;
        max = max || 0;
        const offset = max - min;
        const num = min + Math.random() * offset;

        return isInt ? Math.floor(num) : num;
    }


    /**
     * zpF:
     * @param n 
     * @returns {*} 
     */
    public static convertMoneyToCapitals(n) {
        if (n == 0) {
            return ""
        }
        if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(n))
            return "";
        let unit = "",
            str = "";
        n += "";
        const p = n.indexOf('.');
        if (p >= 0)
            return "";
        unit = unit.substr(unit.length - n.length);
        for (let i = 0; i < n.length; i++)
            str += ''.charAt(n.charAt(i)) + unit.charAt(i);
        return str.replace(/(|||)/g, "").replace(/()+/g, "").replace(/(||)/g, "$1").replace(/()|()/g, "$1$2").replace(/^?|/g, "").replace(/$/g, "");
    };

