    /**
     * zpF 
     * @param str
     * @returns {number}
     */
    public static getStrInBytes(str) {
        let bytesCount = 0;
        for (let i = 0; i < str.length; i++) {
            const c = str.charAt(i);
            if (/^[\u0000-\u00ff]$/.test(c)) //
            {
                bytesCount += 1;
            } else {
                bytesCount += 2;
            }
        }
        return bytesCount
    }

