    /**
     * 
     */
    public static getDistance(gps1, gps2) {
        if (!gps1 || !gps2) return 0;

        console.log("gps", gps1, gps2);

        let arr1 = gps1.split(',');
        let arr2 = gps2.split(',');
        if (arr1 && arr2 && arr1.length === 2 && arr2.length === 2)
            return GameTools.getFlatternDistance(arr1[0], arr1[1], arr2[0], arr2[1]);

        return 0;
    };

    /**
     * 2
     * @param {Object} lat1
     * @param {Object} lng1
     * @param {Object} lat2
     * @param {Object} lng2
     */
    public static getFlatternDistance(lat1, lng1, lat2, lng2) {
        lat1 = parseFloat(lat1);
        lng1 = parseFloat(lng1);
        lat2 = parseFloat(lat2);
        lng2 = parseFloat(lng2);

        let f = GameTools.rad((lat1 + lat2) / 2);
        let g = GameTools.rad((lat1 - lat2) / 2);
        const l = GameTools.rad((lng1 - lng2) / 2);

        let sg = Math.sin(g);
        let sl = Math.sin(l);
        let sf = Math.sin(f);

        let s, c, w, r, d, h1, h2;
        const a = GameTools.EARTH_RADIUS;
        const fl = 1 / 298.257;

        sg = sg * sg;
        sl = sl * sl;
        sf = sf * sf;

        s = sg * (1 - sl) + (1 - sf) * sl;
        c = (1 - sg) * (1 - sl) + sf * sl;

        w = Math.atan(Math.sqrt(s / c));
        r = Math.sqrt(s * c) / w;
        d = 2 * w * a;
        h1 = (3 * r - 1) / 2 / c;
        h2 = (3 * r + 1) / 2 / s;

        let result = d * (1 + fl * (h1 * sf * (1 - sg) - h2 * (1 - sf) * sg));
        result = Math.round(result);
        console.log("", result);

        return result;
    }



    static PI = Math.PI;
    static EARTH_RADIUS = 6378137.0;    //M
    private static rad(d) {
        return d * GameTools.PI / 180.0;
    }


    /**
     * index=12
     * @static
     * @param {*} versionA
     * @param {*} versionB
     * @param {*} idx   -1>0
     * 1.2.3 -11.2.3 011012build
     * @returns 0AB
     * @memberof GameTools
     */
    public static versionCompare(versionA, versionB,idx=1) {
        const vA = versionA.split('.');
        const vB = versionB.split('.');
        let size = Math.min(vA.length,vB.length);
        // 
        if (idx >= 0){
            size = Math.min(idx + 1,size);
        }
        for (let i = 0; i < size; ++i) {
            const a = parseInt(vA[i]);
            const b = parseInt(vB[i] || 0);
           
            if (a === b) {
                continue;
            } else {
                return a - b;
            }
        }
        if (idx == -1 && vB.length > vA.length) {
            return -1;
        } else {
            return 0;
        }
    };


    /**
     * 
     * @static
     * @param {*} versionA
     * @param {*} versionB
     * @returns 0AB
     * @memberof GameTools
     */
    public static versionCompareHandle(versionA, versionB) {
        const vA = versionA.split('.');
        const vB = versionB.split('.');
        for (let i = 0; i < vA.length; ++i) {
            const a = parseInt(vA[i]);
            const b = parseInt(vB[i] || 0);
            if (a === b) {
                continue;
            } else {
                return a - b;
            }
        }
        if (vB.length > vA.length) {
            return -1;
        } else {
            return 0;
        }
    };

    public static setLabelStr(str: string, targetNode: Node) {
        targetNode.getComponent(Label).string = str;
    }

    public static readLocalImg(url: string, targetNode?: Node, trimMode = 0) {
        return new Promise((resolve, reject) => {
            if (url.indexOf(".jpg") != -1 || url.indexOf(".png") != -1) {
                url = url.substr(0, url.length - 4);
            }
            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN, url+"/spriteFrame", SpriteFrame, (err, spriteFrame: SpriteFrame) => {
                if (err) return console.error(err);
                if (!targetNode) return resolve(spriteFrame);
                let sprite = targetNode.getComponent(Sprite);
                if (sprite.spriteFrame != spriteFrame) {
                    sprite.spriteFrame = spriteFrame;
                }
                if (trimMode == 1) {
                    sprite.sizeMode = Sprite.SizeMode.TRIMMED;
                    sprite.trim = true;
                }
                resolve(spriteFrame)
            });
        })
    }


    //aa{0}a{1}aa
    public static getStringReplace(s: string, ...param: any[]): string {
        if (param && param.length) {
            return s.replace(/\{(\d+)\}/g, function (s, i) {
                return param[i];
            })
        } else {
            return s;
        }
    }

    //','
