    public static setGray(root: Node, isGray: boolean = true) {
        if (!root) return;
        //  Sprite  Label 
        let targetSprite = root.getComponent(Sprite);
        let targetLabel = root.getComponent(Label);
        if (targetSprite) {
            targetSprite.grayscale = isGray;
        }
        // 
        root.children.forEach((child) => {
            this.setGray(child, isGray);
        });
    }

    public static nFormatter(num: number, digits = 2) {
        const si = [
            { value: 1, symbol: "" },
            { value: 1E3, symbol: "K" },
            { value: 1E6, symbol: "M" },
            { value: 1E9, symbol: "B" },
            { value: 1E12, symbol: "T" },
            // { value: 1E15, symbol: "P" },
            // { value: 1E18, symbol: "E" }
        ];
        const rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
        let i;
        for (i = si.length - 1; i > 0; i--) {
            if (num >= si[i].value) {
                break;
            }
        }
        return (num / si[i].value).toFixed(digits).replace(rx, "$1") + si[i].symbol;
    }

    /**
     * zpF H5
     * @param name 
     * @returns {string}
     * @constructor
     */
    public static GetQueryString(name) {
        if (sys.isBrowser) { //
            let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
            const r = window.location.search.substr(1).match(reg);
            if (r != null) return unescape(r[2]);
        }

    };
