import { _decorator, Node } from 'cc';
export default class EventCenter extends Node {
    private static _inst:EventCenter = null;
    private static get inst():EventCenter {return this._inst || new EventCenter();}
    public constructor() { super(); EventCenter._inst = this; }
    
    public static onEvent(strEvent:string, callback:Function, caller:any) {
        this.inst.on(strEvent, callback, caller);
    }
    public static offEvent(strEvent:string, callback:Function, caller:any) {
        this.inst.off(strEvent, callback, caller);
    }
    public static emitEvent(strEvent:string, arg1?: any, arg2?: any, arg3?: any, arg4?: any, arg5?: any) {
        this.inst.emit(strEvent, arg1, arg2, arg3, arg4, arg5);
    }
}


