import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";
import { BaseConfigContainer, ConfigData } from "./BaseConfigContainer";
import { ModuleDef } from "db://assets/scripts/ModuleDef";
import { JsonAsset } from "cc";
import { Logger } from "db://assets/core_tgx/base/log/Logger";
import GameTools from "../../kernel/GameTools";
import { GD } from "../../kernel/GD";

export class LanguageData {
    language_id: number;
    zh: string;
    vi: string;
    en: string;
}

const enum LanguageTitle {
    language_id,
    zh,
    vi,
    en
}

export class LanguageConfigContainer extends BaseConfigContainer {
    private configData: ConfigData = null;
    private mapConfig: any = null;

    constructor(callback: Function, caller: any, arg: any) {
        super();
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN,this.CONFIG_FILE_DIR + "language",JsonAsset,(err: Error, jsonAsset: JsonAsset) => {
            if (err) {
                Logger.error("load language err");
            }
            else {
                this.configData = jsonAsset.json as ConfigData;

                if (callback) {
                    callback.call(caller, arg);
                }
            }
        });
    }

    /**
     *  id"language_1234"
     * @param id 
     */
    public getLang(id: string): string {
        if (id.indexOf("language_") == -1) {
            return id;
        }
        let langId: number = Number(id.replace("language_", ""));
        if (isNaN(langId)) {
            return id;
        }
        return this.getLangById(langId);
    }

    public formatLang(id: string, replacementList: string[]): string {
        if (id.indexOf("language_") == -1) {
            return id;
        }
        let langId: number = Number(id.replace("language_", ""));
        if (isNaN(langId)) {
            return id;
        }
        return this.formatLangById(langId, replacementList);
    }

    // 
    public formatLangById(id: number, ...param: any[]): string {
        let str: string = this.getLangById(id);
        let retStr: string = this.format(str, param);
        return retStr;
    }

    /**
     * 
     * @param id ID
     */
    public getLangById(id: number): string {
        let lang = GameTools.GetQueryString("language") || GD.GameConfig.language;
        let langId = id.toString();
        if (this.mapConfig == null) {
            this.mapConfig = {};
            for (let i = 0; i < this.configData.RECORDS.length; i++) {
                let record = this.configData.RECORDS[i];
                let data = new LanguageData();
                data.language_id = record[LanguageTitle.language_id];
                data.zh = record[LanguageTitle.zh];
                data.vi = record[LanguageTitle.vi];
                data.en = record[LanguageTitle.en];
                this.mapConfig[data.language_id] = data;
            }
        }
        let temp: LanguageData = this.mapConfig[langId];
        if (temp) {
            switch (Number(lang)) {
                case 1:
                    return temp.zh;
                case 2:
                    return temp.vi;
                case 3:
                    return temp.en;
                default:
                    return langId;
            }
        }
        return langId;
    }
    

    private format(format, args: any[]): string
    {
        return format.replace(/{(\d+(:\w*)?)}/g,
            (match, i)=>
            { //0
                let s = match.split(':');
                if (s.length > 1)
                {
                    i = i[0];
                    match = s[1].replace('}', ''); //U
                }
                let arg = this.parsePattern(match, args[i]);
                return typeof arg != 'undefined' && arg != null ? arg : "";
            });
    }

    private parsePattern(match, arg):string 
    {
        if (arg == null || arg == undefined)
            return arg;

        switch (match) 
        {
            case 'L':
                arg = arg.toLowerCase();
                break;
            case 'U':
                arg = arg.toUpperCase();
                break;
            case 'd':
            {
                let splitted = arg.split('-');
                if (splitted.length <= 1)
                    return arg;
                let day = splitted[splitted.length - 1];
                let month = splitted[splitted.length - 2];
                let year = splitted[splitted.length - 3];
                day = day.split('T')[0];
                day = day.split(' ')[0];
                arg = day + '.' + month + '.' + year;
            }
                break;
            case 's':
            {
                let splitted = arg.replace(',', '').split('.');
                if (splitted.length <= 1)
                    return arg;
                let time = splitted[splitted.length - 1].split(' ');
                if (time.length > 1)
                    time = time[time.length - 1];
                let year = splitted[splitted.length - 1].split(' ')[0];
                let month = splitted[splitted.length - 2];
                let day = splitted[splitted.length - 3];
                arg = year + "-" + month + "-" + day;
                if (time.length > 1)
                    arg += "T" + time;
                else
                    arg += "T" + "00:00:00";
            }
                break;
            case 'n': //Tausender Trennzeichen
            {
                if (isNaN(parseInt(arg)) || arg.length <= 3)
                    break;
                arg = arg.toString();
                let mod = arg.length % 3;
                let output = (mod > 0 ? (arg.substring(0, mod)) : "");
                for (let i = 0; i < Math.floor(arg.length / 3); i++) {
                    if ((mod == 0) && (i == 0))
                        output += arg.substring(mod + 3 * i, mod + 3 * i + 3);
                    else
                        output += '.' + arg.substring(mod + 3 * i, mod + 3 * i + 3);
                }
                arg = output;
            }
                break;

            default:
                break;
        }
        return arg;
    }
}