import { _decorator, Component, Sprite, Animation, Vec2 } from 'cc';
import Utils from '../../kernel/Utils';
import { AnimationClip } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { easing } from 'cc';
import { Tween } from 'cc';
const {ccclass, property} = _decorator;

@ccclass('PDKFlyGold')
export default class PDK_FlyGold extends Component {
    private m_sprite:Sprite | null = null;
    private m_animation: Animation | null = null;
    private m_flySpeed:number = 1000;
    private m_selfPos: Vec2 | null = null;
    private m_targetPos: Vec2 | null = null
    onLoad () {
        this.m_sprite = this.node.getComponent(Sprite);
        this.m_sprite.enabled = false;

        this.m_animation = this.node.getComponent(Animation);
    }
    public init(x: number, y: number, targetx:number, targety:number) {

        this.m_selfPos = new Vec2();
        this.getRandomPos(x, y, this.m_selfPos);
        this.node.setPosition(x, y);
        this.m_targetPos = new Vec2(targetx, targety);

        let delay:number = Utils.randomRange(0.01, 0.3);
        this.scheduleOnce(this.startAni, delay);
    }
    private startAni() {
        this.m_sprite.enabled = true;
        this.m_animation.defaultClip.wrapMode = AnimationClip.WrapMode.Loop;
        this.readyPosition();
    }
    private readyPosition() {
        this.node.opacity = 0;
        //  tween 
        Tween.stopAllByTarget(this.node);

        // 
        const moveTween = tween(this.node)
           .to(0.5, { position: new Vec3(this.m_selfPos.x,this.m_selfPos.y) });

        // 
        const fadeInTween = tween(this.node)
           .to(0.5, { opacity: 255 });

        // 
        moveTween.call(() => {
                const flyDelay = Utils.randomRange(0.1, 0.3);
                setTimeout(() => {
                    this.startFly();
                }, flyDelay * 1000);
            })
           .start();

        // 
        fadeInTween.start();
    }
    private startFly() {
        this.m_animation.play();
        const distance = Vec2.distance(this.m_targetPos, this.m_selfPos);
        const time = distance / this.m_flySpeed;

        // 
        tween(this.node)
           .to(time, { position: new Vec3(this.m_targetPos.x,this.m_targetPos.y) }, { easing: easing.quadInOut })
           .call(() => {
                this.onFlyComplete();
            })
           .start();
    }
    private onFlyComplete() {
        if (this.node.isValid) {
            this.node.active = false;
            this.node.destroy();
        }
    }
    private getRandomPos(x, y, outVec2: Vec2) {
        outVec2.x = Utils.randomRange(x - 100, x + 100);
        outVec2.y = Utils.randomRange(y - 100, y + 100);
    }
}