export class GameTool {
    //by_009:
    static createPanel(panelName: string, cb?: Function) {
        console.log("create panel : " + panelName);
        GameTool.showWaitLayer(true);
        let canvas = find("Canvas");
        let panelRoot = canvas.getChildByName("panel_root");
        let self = this;
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN, panelName, Prefab, function (err, prefab) {
            GameTool.showWaitLayer(false);
            if (err) {
                console.error(err);
                return
            }
            self.doCreatePanel(panelRoot, prefab, cb);
        });
    }

    // 
    static createPanelByPrefab(prefab: Prefab, cb?: Function) {
        console.log("create panel : " + prefab.name);
        let canvas = find("Canvas");
        let panelRoot = canvas.getChildByName("panel_root");
        this.doCreatePanel(panelRoot, prefab, cb);
    }

    static doCreatePanel(panelRoot: Node, prefab: Prefab,cb?: Function):Node {

        let newNode = instantiate(prefab);
        newNode.parent = panelRoot;
        newNode.setPosition(0, 0);
        newNode.opacity = 255;
        newNode.active = true;
        GameTool.updateWidget(newNode);


        //
        let sprite_splash = find("panel/sprite_splash", newNode);
        if (sprite_splash) {
            sprite_splash.once(NodeEventType.TOUCH_END, function () {
                // AudioControl.playClick();
                newNode.destroy();
            }.bind(this), this)
        }
        //
        let btn_close = find("panel/btn_close", newNode);
        if (btn_close) {
            btn_close.once(NodeEventType.TOUCH_END, function () {
                // AudioControl.playClick();
                newNode.destroy();
            }.bind(this), this)
        }
        cb && cb(newNode);
        cb = null;
        return newNode;
    }

    /**by_009:*/
    static updateWidget(targetNode: Node) {
        let widgets:Widget[] = targetNode.getComponentsInChildren(Widget);
        const currentScene = director.getScene();
        if (currentScene) {
            //  Canvas 
            const canvasComponent = currentScene.getComponentInChildren(Canvas);
            if (canvasComponent) {
                //  Canvas 
                const canvasNode = canvasComponent.node;
                for (let i = 0; i < widgets.length; i++) {
                    widgets[i].target = canvasNode;
                    widgets[i].updateAlignment();
                }
            } else {
                console.error(' Canvas ');
            }
        }
    }

    //by_009:
    static createPrefab(panelName: string, cb?: Function) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN, panelName, Prefab, function (err, prefab) {
            if (err) {
                console.error(err);
                return
            }
            let newNode = instantiate(prefab);
            if (cb) {
                cb(newNode);
                cb = null;
            }
        });
    }

    //by_009:
    static showTextTips(value: string) {
        let canvas = find("Canvas");
        let panelRoot = canvas.getChildByName("panel_root");
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN, "kernel/PublicPrefab/UITips/UITips", Prefab, function (err, prefab) {
            let ui:Node = GameTool.doCreatePanel(panelRoot, prefab);
            ui.getComponent(UITips).showText(value);
        });
    }
    public static setWaitNode(prefab:Prefab)
    {
        GameTool._waitNode = instantiate(prefab);
        GameTool._waitNode.active = false;
        let parent = UIMgr.inst.getLayerNode(GameUILayers.LOADING);
        parent.addChild(GameTool._waitNode);
       
    }
    private static _waitNode:Node;
    static showWaitLayer(isShow:boolean)
    {
        if(GameTool._waitNode)
        {
            GameTool._waitNode.getComponent(UILoading).showPanel(isShow,"");
        }
    }
    static destroyWaitLayer()
    {
        if(GameTool._waitNode)
        {
            GameTool._waitNode.destroy();
            GameTool._waitNode = null;
        }
        
    }
    //by_009:
    static messageBox(text: string, cb1?, cb2?) {
        GameTool.createPanel("kernel/PublicPrefab/UIDialogBox/UIMessageBox", (newNode) => {
            find("panel/content", newNode).getComponent(Label).string = text || "";
            find("panel/btns/btn_confirm", newNode).on(NodeEventType.TOUCH_END, () => {
                if (cb1) {
                    cb1();
                }
                newNode.destroy();
            }, this);
            find("panel/btns/btn_cancel", newNode).on(NodeEventType.TOUCH_END, () => {
                if (cb2) {
                    cb2();
                }
                newNode.destroy();
            }, this);
            find("panel/btns/btn_cancel", newNode).active = !!cb2;
        })
    }


    // 
    static showRuletip(rule_txt) {
        // if (rule_txt) {
        //     GD.GameTool.createPanel("hall/prefab/panel_rule_tip", (newNode) => {
        //         let panel: panel_rule_tip = newNode.getComponent(panel_rule_tip);
        //         if (panel != null) {
        //             panel.updateView(rule_txt);
        //         }
        //     })
        // }
    }

    //
    static updateGoldSettlementType() {
        let gold_settlementType = sys.localStorage.getItem("gold_settlementType");
        if (gold_settlementType) {
            GD.goldSettlementType = gold_settlementType;
        } else {
            GD.goldSettlementType = 1;
        }
    }
}


