import { Tween } from 'cc';
import { tween } from 'cc';
import { _decorator, Component, Sprite, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PDKEmojiComp')
export default class PDK_EmojiComp extends Component {
    private m_sprite: Sprite;
    private m_assets: SpriteFrame[];
    private m_totalIndex: number = 0;
    private m_currentIndex: number = 0;
    private m_isAnim: boolean = false;
    private m_deltaTime:number = 0;
    private SWITCH_IMG_TIME: number = 0.2;
    onLoad() {
        if (this.m_sprite == null) {
            this.m_sprite = this.node.getComponent(Sprite);
        }
    }
    public startFrameAnimation(assets: SpriteFrame[]) {
        this.getSprite();
        if (this.m_sprite == null) return;
        this.m_assets = assets;
        this.m_totalIndex = assets.length;
        this.m_currentIndex = 0;
        this.m_deltaTime = 0;
        this.m_isAnim = true;
        this.m_sprite.spriteFrame = assets[0];
        this.node.active = true;
        this.node.opacity = 0;
        //  tween 
        Tween.stopAllByTarget(this.node);

        // 
        tween(this.node)
           .to(0.2, { opacity: 255 })
           .start();
    }
    update(dt: number) {
        if (this.m_isAnim) {
        if (this.m_sprite == null) return;
        this.m_deltaTime += dt;
        if (this.m_deltaTime >= this.SWITCH_IMG_TIME) {
        this.m_deltaTime = 0;
        this.m_currentIndex ++;
        if (this.m_currentIndex != this.m_totalIndex) {
        this.m_sprite.spriteFrame = this.m_assets[this.m_currentIndex];
        }
        else {
        this.onCompleteAnim();
        }
        }
        }
    }
    private onCompleteAnim() {
        this.node.active = false;
        this.m_isAnim = false;
    }
    private getSprite() {
        if (this.m_sprite == null) {
        this.m_sprite = this.node.getComponent(Sprite);
        }
    }
}
