    private startBattleSort() {
        if (this.m_btnSort != null) {
            this.m_btnSort.active = true;
            this.m_btnSort.opacity = 0;
            Tween.stopAllByTarget(this.m_btnSort);
            this.sortCard();
        }
    }
    sortCard() {
        this.m_sorting = true;

        let playerNode = this.playerScript[0];
        if (!playerNode) return;

        let player:PDK_Player = playerNode.getComponent(PDK_Player);
        if (!player || player.userId !== UserMgr.inst.uid) return;

        let shouPaiNode = player.shouPai;
        if (!shouPaiNode) return;

        //  tween 
        Tween.stopAllByTarget(shouPaiNode);

        // 
        const scaleSmallTween = tween(shouPaiNode)
           .to(0.2, { scale: new Vec3(0, 1, 1) }, { easing: easing.quartIn });

        // 
        const scaleNormalTween = tween(shouPaiNode)
           .to(0.2, { scale: new Vec3(1, 1, 1) }, { easing: easing.backOut });

        // 
        tween(shouPaiNode)
           .then(scaleSmallTween)
           .call(() => this.doSortCard())
           .then(scaleNormalTween)
           .call(() => this.onSortCardComplete())
           .start();
    }
    private doSortCard() {
        let allCard = this.getAllCard();
        let sortArr = null;

        if (this.sortType == 2 || this.sortType == 0) {
        this.sortType = 1;
        sortArr = this.GameLogic.sort(allCard);
        console.log(sortArr);
        }
        else if (this.sortType == 1) {
        this.sortType = 2;
        sortArr = this.GameLogic.sortByCardType(allCard);
        console.log(sortArr);
        }
        if (sortArr != null) {
        this.playerScript[0].fillShouPai(sortArr);
        }
        EventCenter.emitEvent(gameExpressionEvent.PUT_MY_ALL_CARDS_BACK);
    }
    private onSortCardComplete() {
        this.m_sorting = false;
    }
