    private showBombAni(data: MsgBombScore) {
       // 
        EventCenter.emitEvent(gameExpressionEvent.SHOW_BOMB);
        let { scoreRecords } = data;
       // 
        let dstPlayer: PDK_Player = null;
        let srcPlayer: PDK_Player = null;
        let addScore: number = 0;
        let subScore: number = 0;
        let seat: number = -1;
        for (let i = 0; i < scoreRecords.length; i++) {
            let record = scoreRecords[i];
            let chairID = record.chairID;
            let score = record.score;
            let seatID = PokerUtils.getRelativeSeatID(chairID, PokerManager.inst.myChairID);
            let player: PDK_Player = this.playerScript[seatID];
            if (player == null) continue;
            if (score > 0) {
                dstPlayer = player;
                addScore = score;
                seat = seatID;
            }
            else {
                srcPlayer = player;
                subScore = score;
            }
            }
            if (dstPlayer != null && srcPlayer != null && seat != -1) {
                dstPlayer.showHeadScoreAni(addScore);
                srcPlayer.showHeadScoreAni(subScore);
                srcPlayer.showGoldFly(seat);
                let soundUrl: string = "sound/coin_fly"; // 
                PDK_PlayerSoundHelper.playSoundUrl(soundUrl);
                this.scheduleOnce(() => {
                if (dstPlayer != null && dstPlayer.node.isValid && PDK_BattleData.instance().isInBattle) {
                dstPlayer.headAni.showGoldFall();
                }
            }, 2);
            dstPlayer.headAni.hideScoreAni(5);
            srcPlayer.headAni.hideScoreAni(5);
        }
    }
