import { PokerManager } from "db://assets/module_basic/scripts/poker/PokerManager";
import { CARD_COLOR, CARD_RANK } from "./Define";

export class PokerUtils{
    
     /**
     * 3
     * 
     * @param oldCards 
     */
     spade3Check(deck:number[], playerCount:number)
     {
         if ((playerCount * 13) >= deck.length) {
             return true;
         }
 
         let spade3Index = deck.indexOf(0x03);
         if (spade3Index >= (playerCount * 13 - 1)) {
             // 
             let randomIndex = Math.floor(Math.random() * playerCount * 13);
             deck[spade3Index] = deck[randomIndex];
             deck[randomIndex] = 0x03;
         }
 
         return true;
     }
     /**
     * 
     *
     * @param card 
     */
    public static getCardRank(card: number):CARD_RANK {
        return (card & 0x0F);
    }
    
    /**
     * 
     *
     * @param card 
     */
    public static getCardColor(card: number):CARD_COLOR {
        return (card & 0xF0) >> 4;
    }

    /**
     * 
     * @param chairID id
     * @param myChairID id
     * @param chairCount 
     */
    public  static getRelativeSeatID(chairID:number, myChairID:number, chairCount:number=0) 
    {
        if(chairCount==0)
        {
            chairCount = PokerManager.inst.roomData.playerCount;
        }
        return (chairID + chairCount - myChairID) % chairCount;
    }

}