const rootPath = "sound/";

//by_009:
export const AudioControl = {
    //by_009:
    playClick() {
        let path = rootPath+"click";
        tgx.AudioMgr.inst.audio.playEffect(path,ModuleDef.POKER_PDK_YUENAN);
    },

    //by_009:
    playBGM() {
        let path = rootPath+"BGM";
        tgx.AudioMgr.inst.audio.playMusicLoop(path,ModuleDef.POKER_PDK_YUENAN);
    },

    //by_009:
    playFinish() {
        let path = rootPath+"finish";
        tgx.AudioMgr.inst.audio.playEffect(path,ModuleDef.POKER_PDK_YUENAN);
    },

    //
    playGetReward() {
        let path = rootPath+"gifteffect";
        tgx.AudioMgr.inst.audio.playEffect(path,ModuleDef.POKER_PDK_YUENAN);
    }
    
}
