import { _decorator, Component, Node } from 'cc';
import { LanguageConfigContainer} from './Conifg/LanguageConfigContainer';
import { ConfigManager } from './ConfigManager';
import { find } from 'cc';
import { Label } from 'cc';
import { Button } from 'cc';
import { AudioControl } from './AudioControl';
import { GameTool } from '../kernel/GD';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
const { ccclass, property } = _decorator;


@ccclass('PDKSetting')
export default class PDK_Setting extends Component {
    @property(Node)
    btn_music: Node | null = null;
    @property(Node)
    btn_effect: Node | null = null;
//    // LIFE-CYCLE CALLBACKS:
    onLoad() {
        let langCfg: LanguageConfigContainer = ConfigManager.getInstance().getConfig(LanguageConfigContainer) as LanguageConfigContainer;
        let lbl = find("panel/content/btn_exit/New Label", this.node).getComponent(Label);
        lbl.string = langCfg.getLangById(10251);

        let array = [this.btn_effect, this.btn_music];
        for (let m = 0; m < array.length; m++) {
            let element = array[m];
            let on_lbl = element.getChildByName("bg_on").getChildByName("New Label");
            let off_lbl = element.getChildByName("bg_off").getChildByName("New Label");
            on_lbl.getComponent(Label).string = langCfg.getLangById(11803);
            off_lbl.getComponent(Label).string = langCfg.getLangById(11804);
        }
    }
    start() {
        let isBgmOpen =  tgx.AudioMgr.inst.audio.switchMusic;
        this.btn_music.children[2].active = isBgmOpen;
        this.btn_music.children[1].active = !isBgmOpen;
        let isEffectOpen = tgx.AudioMgr.inst.audio.switchEffect;
        this.btn_effect.children[2].active = isEffectOpen;
        this.btn_effect.children[1].active = !isEffectOpen;

        let btns = this.node.getComponentsInChildren(Button);
        for (let i = 0; i < btns.length; i++) {
            btns[i].node.on(Node.EventType.TOUCH_END, this.onBtnClick, this);
        }
    }
    clickClose() {
        this.node.destroy();
    }
    onBtnClick(event) {
        AudioControl.playClick();
        let name = event.currentTarget.name;
        switch (name) {
            case "btn_music":

                let isBgmOpen =  tgx.AudioMgr.inst.audio.switchMusic;
                event.currentTarget.children[2].active = !isBgmOpen;
                event.currentTarget.children[1].active = isBgmOpen;
                tgx.AudioMgr.inst.audio.switchMusic = !isBgmOpen;
                break;
            case "btn_effect":
                let isEffectOpen = tgx.AudioMgr.inst.audio.switchEffect;
                event.currentTarget.children[2].active = !isEffectOpen;
                event.currentTarget.children[1].active = isEffectOpen;
                tgx.AudioMgr.inst.audio.switchEffect = !isEffectOpen;
                break;
            case "btn_close":
                this.clickClose();
                break;
            case "btn_exit":
                this.clickClose();
                tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
                    if (ok) {
                        tgx.AudioMgr.inst.audio.clearAll();
                        RoomMgr.inst.exitRoom();
                    }
                });
                break;
        }
    }
   // update (dt) {}
}
