import { tween } from 'cc';
import { Tween } from 'cc';
import { UITransform } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PDKCardTitle')
export default class PDK_CardTitle extends Component {
    private m_4ji: Node;
    private m_lian3dui: Node;
    private m_lian4dui: Node;
    private m_shuang2: Node;
    private m_chupai: Node;
    private m_seatIdx: number = 0;
    private m_isReady: boolean = true;
    onLoad() {
        this.m_4ji = this.node.getChildByName("four_ji");
        this.m_4ji.active = false;
        this.m_lian3dui = this.node.getChildByName("lian3dui");
        this.m_lian3dui.active = false;
        this.m_lian4dui = this.node.getChildByName("lian4dui");
        this.m_lian4dui.active = false;
        this.m_shuang2 = this.node.getChildByName("shuang2");
        this.m_shuang2.active = false;

        this.m_chupai = this.node.parent.getChildByName("chuPai");
        let name = this.node.parent.name;
        name = name.replace("seat_", "");
        this.m_seatIdx = Number(name);
    }
    ready() {
        this.m_isReady = true;
        this.hideAll();
    }
    private getChupaiCenterX(): number {
        let x = 0
        if (this.m_chupai == null) return x;

        const uiTransform = this.m_chupai.getComponent(UITransform);
        const size = uiTransform.contentSize;
        const scale = this.m_chupai.scale;
        if (this.m_seatIdx == 0) {
            x = this.m_chupai.position.x;
        }
        else if (this.m_seatIdx == 3) {
            x = this.m_chupai.position.x + (size.width / 2 * scale.x);
        }
        else {
            x = this.m_chupai.position.x - (size.width / 2 * scale.x);
        }

        return x;
    }
    private showTitle(title:Node) {
        if (title == null || !this.m_isReady) return;
        this.m_isReady = false;
        this.scheduleOnce(() => {
            let x = this.getChupaiCenterX();
            title.active = true;
            title.opacity = 0;
            let y = this.m_chupai.y - 25;
            if (this.m_seatIdx == 0) {
                y = this.m_chupai.y - 50;
            }
            title.setPosition(x, y);
            //  tween 
            tween(title)
               .to(0.2, { opacity: 255 })
               .start();
        });
    }
    private hideTitle(title:Node) {
        if (title == null) return;
            Tween.stopAllByTarget(title);
        if (title.active) {
            title.active = false;
        }
    }
    private hideAll() {
        this.hideTitle(this.m_4ji);
        this.hideTitle(this.m_lian3dui);
        this.hideTitle(this.m_lian4dui);
        this.hideTitle(this.m_shuang2);
    }
    public show4ji() {
        this.showTitle(this.m_4ji);
    }
    public showLian3Dui() {
        this.showTitle(this.m_lian3dui);
    }
    public showLian4Dui() {
        this.showTitle(this.m_lian4dui);
    }
    public showShuang2() {
        this.showTitle(this.m_shuang2);
    }
}