import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";
import { BaseConfigContainer, ConfigData } from "./BaseConfigContainer";
import { ModuleDef } from "db://assets/scripts/ModuleDef";
import { JsonAsset } from "cc";
import { Logger } from "db://assets/core_tgx/base/log/Logger";

export class FaceData {
    id: number;
    isShow: boolean;
    icon: string;
    res: string;
    num: number;
    sound: number;
}

const enum FaceDataTitle {
    face_id,
    sort,
    show,
    cycle,
    face_icon,
    face_res,
    face_number,
    pei_music_id
}

export class FaceConfigContainer extends BaseConfigContainer {
    private configData: ConfigData = null;
    private mapConfig = null;
    private arrConfig:FaceData[] = null;

    constructor(callback: Function, caller: any, arg: any) {
        super();
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN,this.CONFIG_FILE_DIR + "face",JsonAsset,(err: Error, jsonAsset: JsonAsset) => {
            if (err) {
                Logger.error("load face err");
            }
            else {
                // let encode = object.text;
                // let decode = window.atob(encode);
                // object = JSON.parse(decode);
                // for (var i in object) {
                //     this.monsterConfigData[i] = object[i];
                // }
                // if (callback) {
                //     callback.call(caller, arg);
                // }
                this.configData = jsonAsset.json as ConfigData;

                if (callback) {
                    callback.call(caller, arg);
                }
            }
        }
        );
    }

    private generateMapConfig() {
        if (this.mapConfig == null) {
            this.mapConfig = {};
            this.arrConfig = [];
            for (let i = 0; i < this.configData.RECORDS.length; i++) {
                let record = this.configData.RECORDS[i];
                let temp = new FaceData();
                temp.id = Number(record[FaceDataTitle.face_id]);   // number 
                temp.isShow = record[FaceDataTitle.show] == "1";
                temp.icon = record[FaceDataTitle.face_icon];
                temp.res = record[FaceDataTitle.face_res];
                temp.num = record[FaceDataTitle.face_number];
                temp.sound = record[FaceDataTitle.pei_music_id];
                this.mapConfig[temp.id] = temp;
                this.arrConfig.push(temp);
            }
        }
    }


    getDataArray(): FaceData[] {
        if (this.mapConfig == null) {
            this.generateMapConfig();
        }
        return this.arrConfig;
    }

    getFaceDataById(id): FaceData {
        let faceData: FaceData = null;
        if (this.mapConfig == null) {
            this.generateMapConfig();
        }
        let numId = 0;
        if (typeof id == "string") {
            numId = Number(id);
        }
        else {
            numId = id;
        }
        faceData = this.mapConfig[numId];

        return faceData;
    }

}