import { _decorator, Component, Label, Node } from 'cc';
const {ccclass, property} = _decorator;

import { find } from 'cc';
import { UserResultData } from './message/GameManager';
import { UserMgr } from '../../module_basic/scripts/UserMgr';
import { PokerManager } from '../../module_basic/scripts/poker/PokerManager';

@ccclass('PDKResultPlayer')
export default class PDK_Result_Player extends Component {
    @property(Label)
    lblzha: Label | null = null;
    @property(Label)
    lblchou: Label | null = null;
    
    @property(Label)
    lblScore: Label | null = null;
    @property(Label)
    lblTotal: Label | null = null;
    @property(Node)
    selfMark: Node | null = null;
    @property(Label)
    lblNickname: Label | null = null;
    @property(Node)
    icon_creator: Node | null = null;
    @property(Node)
    head: Node | null = null;
//    // LIFE-CYCLE CALLBACKS:
//    // onLoad () {}
    initPlayerInfo (data: UserResultData, chairID) {
        this.lblScore.string = data.winGold + "";
        // this.lblzha.string = data.bombGold + "";

        let bg = find("bg",this.node);
        if(data.userID == UserMgr.inst.uid){
            bg.active = false;
            this.selfMark.active = true;
        }else{
            bg.active = true;
            this.selfMark.active = false;
        }


       let tableUser = PokerManager.inst.getTableUserItem(chairID);
       if (tableUser == null) {
           console.error("initPlayerInfo error : tableUser");
           return;
       }

        this.lblNickname.string = data.nickname;

        this.icon_creator.active = data.isCreator;

        // this.head && (GameTools.loadWxHead(this.head, data.headUrl));
    }
   // update (dt) {}
}
