import { BaseConfigContainer, ConfigData } from "./BaseConfigContainer";
import { decodeConfigItemString } from "./ItemConfigContainer";
import { LanguageConfigContainer } from "./LanguageConfigContainer";
import { ConfigManager } from "../ConfigManager";
import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";
import { ModuleDef } from "db://assets/scripts/ModuleDef";
import { JsonAsset } from "cc";
import { Logger } from "db://assets/core_tgx/base/log/Logger";

export class GlobalData {
    config_id;
    data;
}

const enum GlobalDataTitle {
    config_id,
    data
}

export class GlobalConfigContainer extends BaseConfigContainer {
    private globalConfigData: ConfigData = null;
    private mapConfig = null;
    constructor(callback: Function, caller: any, arg: any) {
        super();
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN,this.CONFIG_FILE_DIR + "config",JsonAsset,(err: Error, jsonAsset: JsonAsset) => {
            if (err) {
                Logger.error("load language err");
            }
            else {
                this.globalConfigData = jsonAsset.json as ConfigData;

                if (this.mapConfig == null) {
                    this.mapConfig = {};
                    for (let i = 0; i < this.globalConfigData.RECORDS.length; i++) {
                        let record = this.globalConfigData.RECORDS[i];
                        let temp = new GlobalData();
                        temp.config_id = Number(record[GlobalDataTitle.config_id]);   // 
                        let data = Number(record[GlobalDataTitle.data]);
                        if (isNaN(data)) {
                            temp.data  = record[GlobalDataTitle.data]
                        }
                        else {
                            temp.data = data; 
                        }
                          // 
                        this.mapConfig[temp.config_id] = temp;
                    }
                }

                if (callback) {
                    callback.call(caller, arg);
                }
            }
        }
        );
    }

    getGlobalConfigData(): ConfigData {
        return this.globalConfigData;
    }

    getDataByConfigId(config_id) {
        let configData = this.mapConfig[config_id];
        return configData.data;
    }
}