import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";
import { ConfigManager } from "../ConfigManager";
import { BaseConfigContainer, ConfigData } from "./BaseConfigContainer";
import { LanguageConfigContainer } from "./LanguageConfigContainer";
import { ModuleDef } from "db://assets/scripts/ModuleDef";
import { JsonAsset } from "cc";
import { Logger } from "db://assets/core_tgx/base/log/Logger";

export class ItemData {
    item_id: number;
    name: string;
    desc: string;
    icon: string;
    count: number;
}

export class ItemInfo {
    itemId: number;
    count: number;
}

const enum ItemDataTitle {
    item_id,
    name,
    desc,
    icon
}

export function decodeConfigItemString(itemStr: string): ItemInfo[] {
    let itemList = [];
    let list = itemStr.split("#");
    for (let i = 0; i < list.length; i++) {
        let data = list[i].split("_");
        let itemId = Number(data[0]);
        let count = Number(data[1]);
        itemList.push({ itemId: itemId, count: count });
    }
    return itemList;
};

export class ItemConfigContainer extends BaseConfigContainer {
    private itemConfigData: ConfigData = null;

    constructor(callback: Function, caller: any, arg: any) {
        super();
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN,this.CONFIG_FILE_DIR + "item",JsonAsset,(err: Error, jsonAsset: JsonAsset) => {
            if (err) {
                Logger.error("load item err");
            }
            else {
                this.itemConfigData = jsonAsset.json as ConfigData;

                if (callback) {
                    callback.call(caller, arg);
                }
            }
        }
        );
    }

    getItemConfigData(): ConfigData {
        return this.itemConfigData;
    }

    getItemDataById(itemId): ItemData {
        let itemData = new ItemData();
        for (let i = 0; i < this.itemConfigData.RECORDS.length; i++) {
            let record = this.itemConfigData.RECORDS[i];
            if (record[ItemDataTitle.item_id] == itemId) {
                itemData.item_id = itemId;
                itemData.desc = record[ItemDataTitle.desc];
                itemData.name = record[ItemDataTitle.name];
                itemData.icon = record[ItemDataTitle.icon];
                let langConfig = ConfigManager.getInstance().getConfig(LanguageConfigContainer) as LanguageConfigContainer;
                itemData.name = langConfig.getLang(itemData.name)
                return itemData;
            }
        }
        return null;
    }

}