import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";
import { BaseConfigContainer, ConfigData } from "./BaseConfigContainer";
import { ModuleDef } from "db://assets/scripts/ModuleDef";
import { JsonAsset } from "cc";
import { Logger } from "db://assets/core_tgx/base/log/Logger";

export class PeiData {
    id: number;
    sound: string;

    arrSound: string[];
}

const enum PeiDataTitle {
    id,
    pei_music_id
}

export class PeiMusicConfigContainer extends BaseConfigContainer {
    private configData: ConfigData = null;
    private mapConfig = null;
    private arrConfig: PeiData[] = null;

    constructor(callback: Function, caller: any, arg: any) {
        super();
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_PDK_YUENAN,this.CONFIG_FILE_DIR + "pei_music",JsonAsset,(err: Error, jsonAsset: JsonAsset) => {
            if (err) {
                Logger.error("load pei_music err");
            }
            else {
                // let encode = object.text;
                // let decode = window.atob(encode);
                // object = JSON.parse(decode);
                // for (var i in object) {
                //     this.monsterConfigData[i] = object[i];
                // }
                // if (callback) {
                //     callback.call(caller, arg);
                // }
                this.configData = jsonAsset.json as ConfigData;

                if (callback) {
                    callback.call(caller, arg);
                }
            }
        });
    }

    private generateMapConfig() {
        if (this.mapConfig == null) {
            this.mapConfig = {};
            this.arrConfig = [];
            for (let i = 0; i < this.configData.RECORDS.length; i++) {
                let record = this.configData.RECORDS[i];
                let temp = new PeiData();
                temp.id = Number(record[PeiDataTitle.id]);   // number 
                temp.sound = record[PeiDataTitle.pei_music_id];
                temp.arrSound = temp.sound.split('#');
                this.mapConfig[temp.id] = temp;
                this.arrConfig.push(temp);
            }
        }
    }


    getDataArray(): PeiData[] {
        if (this.mapConfig == null) {
            this.generateMapConfig();
        }
        return this.arrConfig;
    }

    getDataById(id): PeiData {
        let data: PeiData = null;
        if (this.mapConfig == null) {
            this.generateMapConfig();
        }
        let numId = 0;
        if (typeof id == "string") {
            numId = Number(id);
        }
        else {
            numId = id;
        }
        data = this.mapConfig[numId];

        return data;
    }
}