import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

import PDK_Card from "../PDK_Card";

@ccclass('PDKCardFanSlot')
export default class PDK_CardFanSlot extends Component {
    @property(Node)
    slot: Node | null = null;
    cardId: number = 0;
    card: PDK_Card = null;
}
