    private showRoundResult(userData: UserResultData[]) {
        let langCfg: LanguageConfigContainer = ConfigManager.getInstance().getConfig(LanguageConfigContainer) as LanguageConfigContainer;
        let lbl = find("btn_continue/label", this.node).getComponent(Label);
        lbl.string = langCfg.getLangById(10826);

        this.roundRoot.active = true;
        this.bigRoot.active = false;
        let isWin: boolean = false;
        this.roundLayout.removeAllChildren();

        let selfNode = null;
        for (let i = 0; i < userData.length; i++) {
            let data: UserResultData = userData[i];
            let itemNode = instantiate(this.roundResultItem);
            itemNode.parent = this.roundLayout;
            itemNode.getComponent(PDK_Result_Player).initPlayerInfo(data, i);
            if(data.userID == UserMgr.inst.uid)
            {
                isWin = data.isWinner;
                selfNode = itemNode;
            }

            let originx = itemNode.x;
            itemNode.x = 1500;
            tween(itemNode).delay(0.5 * i).to(0.5, { x: originx }, { easing: easing.backOut }).start();
        }

        let selfMark = find("selfMark", selfNode);
        tween(selfMark).delay(1 * (userData.length - 1)).to(0.2, { opacity: 0 }).delay(0.2).to(0.2, { opacity: 255 }).start();


        let winNode = find("result/bg/title_win", this.node);
        let loseNode = find("result/bg/title_lose", this.node);
        winNode.active = isWin;
        loseNode.active = !isWin;
    }
