import { PrevCards } from "./message/GameManager";
import { CARD_TYPE, CARD_RANK,CARD_SUIT,CARD_COLOR } from "./PDK_ConstDef";



interface cardGroupData {
    type: number,
    cards: number[],
    value: number
}

export default class TienlenLogic {

    /**
     * 3
     * 
     * @param oldCards 
     */
    spade3Check(deck:number[], playerCount:number)
    {
        if ((playerCount * 13) >= deck.length) {
            return true;
        }

        let spade3Index = deck.indexOf(0x03);
        if (spade3Index >= (playerCount * 13 - 1)) {
            // 
            let randomIndex = Math.floor(Math.random() * playerCount * 13);
            deck[spade3Index] = deck[randomIndex];
            deck[randomIndex] = 0x03;
        }

        return true;
    }

    /**
     * 
     *
     * @param card 
     */
    getCardRank(card: number) {
        return (card & 0x0F);
    };
    
    /**
     * 
     *
     * @param card 
     */
    getCardSuit(card: number) {
        return (card & 0xF0) >> 4;
    };

    /**
     * 
     *
     * @param card 
     */
    getCardWeight(card: number) {
        let rank = this.getCardRank(card);
        if (rank > 2) return rank - 2;
        return rank + 11;
    };

    /**
     * 
     *
     * @param card 
     */
     getCardColor(card: number) {
        return this.getCardSuit(card) > 1 ? 1 : 0;
    };

    /**
     * 
     * @param count 
     * @param value 
     */
    initArray(count: number, value: any = 0) {
        value = value || 0;
        var arr = [];
        for (var i = 0; i < count; i++) {
            arr.push(value);
        }
        return arr;
    };

    /**
     * 
     *
     * @param card 
     */
    getSortValue(card: number) {
        return (this.getCardWeight(card) * 4 + this.getCardSuit(card));
    };

    /**
     * 
     * @param cardList 
     */
    sort(cardList: number[]) {
        cardList.sort((a, b) => {
            return this.getSortValue(a) - this.getSortValue(b);
        });
        return cardList;
    };

    /**
     * 
     * @param cardList 
     */
    sortByCardType(cardList: number[]) {
        let targetCardList: number[] = []; // 
        let tempCardList: number[] = [...cardList];
        
        for (let i = 0; i < cardList.length; i++) {
            let ret = this.findPromptCards(tempCardList);
            let targetCards = [];
            if (ret.pairs.length > 0) {
                if (ret.four.length > 0) {
                    // 
                    let hasPairs4 = false;
                    for (let temp of ret.pairs) {
                        if (temp.cards.length >= 8) {
                            targetCards = temp.cards;
                            hasPairs4 = true;
                            break;
                        }
                    }

                    if (!hasPairs4) {
                        targetCards = ret.four[0].cards;
                    }
                } else {
                    targetCards = ret.pairs[0].cards;
                }
            } else if (ret.four.length > 0) {
                targetCards = ret.four[0].cards;
            } else if (ret.straight.length > 0) {
                targetCards = ret.straight[0].cards;
            } else if (ret.three.length > 0) {
                targetCards = ret.three[0].cards;
            } else if (ret.pair.length > 0) {
                targetCards = ret.pair[0].cards;
            } else if (ret.single.length > 0){
                targetCards = ret.single[0].cards;
            } else {
                console.error("sort cards error.")
                // throw new Error("cards error")
            }

            // 
            targetCardList.push(...targetCards);
            // 
            let playRet = this.playCards(tempCardList, targetCards);
            if (playRet.leftCards.length == 0) {
                break;
            }

            tempCardList = playRet.leftCards;
        }

        for (let i in cardList) {
            cardList[i] = targetCardList[i];
        }
        return targetCardList;
    };

    greaterThan(card1: number, card2: number)
    {
        return this.getSortValue(card1) > this.getSortValue(card2);
    }

    /**
     * 
     * @param card1 1
     * @param card2 2
     */
    checkPair(card1: number, card2: number)
    {
        if (this.getCardRank(card1) == this.getCardRank(card2)) {
            return true;
        }

        return false;
    }

    /**
     * 
     * @param card1 1
     * @param card2 2
     */
    checkContinuous(card1: number, card2: number) {
        let rank1 = this.getCardRank(card1);
        let rank2 = this.getCardRank(card2);
        if (rank1 + 1 == rank2 || (rank1 == CARD_RANK.KING && rank2 == CARD_RANK.ACE)) {
            return true;
        }

        return false;
    }

    /**
     * 
     * @param card1 1
     * @param card2 2
     * @param card3 3
     */
    checkThree(card1: number, card2: number, card3: number) {
        if (this.checkPair(card1, card2) && this.checkPair(card2, card3)) {
            return true;
        }

        return false;
    }

    /**
     * 
     * @param card1 1
     * @param card2 2
     * @param card3 3
     * @param card4 4
     */
     checkFour(card1: number, card2: number, card3: number, card4: number) {
        if (this.checkPair(card1, card2) && this.checkPair(card2, card3) && this.checkPair(card3, card4)) {
            return true;
        }

        return false;
    }

    /**
     * 
     *
     * @param cards 
     */
    getCardType(cards: number[]) {
        this.sort(cards);

        let n = cards.length;

        if (n == 1) {
            return CARD_TYPE.SINGLE;
        }

        if (n == 3) {
            if (this.checkThree(cards[0], cards[1], cards[2])) {
                return CARD_TYPE.THREE;
            }
        }

        if (n % 2 == 0) {
            if (n == 2) {
                if (this.checkPair(cards[0], cards[1])) {
                    return CARD_TYPE.PAIR;
                } else {
                    return CARD_TYPE.NULL;
                }
            }

            if (n == 4) {
                if (this.checkFour(cards[0], cards[1], cards[2], cards[3])) {
                    return CARD_TYPE.FOUR;
                }
            }

            if (n >= 6) {
                if (this.checkPairs(cards)) {
                    return CARD_TYPE.PAIRS;
                }
            }
        }

        if (this.checkStraight(cards)) {
            return CARD_TYPE.STRAIGHT;
        }

        return CARD_TYPE.NULL;
    };

    /**
     * 
     *
     * @param cards 
     */
    checkPairs(cards: number[])
    {
        if (cards.length < 6 || cards.length % 2 != 0) {
            return false;
        }

        // 2
        if (this.getCardRank(cards[cards.length - 1]) == CARD_RANK.TWO) {
            return false;
        }

        for (let i = 0; i < cards.length - 3; i += 2) {
            // 
            if (!this.checkPair(cards[i], cards[i + 1]) || !this.checkContinuous(cards[i], cards[i + 2])) {
                return false;
            }
        }

        // 
        if (!this.checkPair(cards[cards.length - 2], cards[cards.length - 1])) {
            return false;
        }

        return true;
    }

    /**
     * 
     *
     * @param cards 
     */
    checkStraight(cards: number[])
    {
        // 2
        if (cards.length < 3 || this.getCardRank(cards[cards.length - 1]) == CARD_RANK.TWO) {
            return false;
        }

        for (let i = 0; i < cards.length - 1; i++) {
            if (!this.checkContinuous(cards[i], cards[i + 1])) {
                return false;
            }
        }

        return true;
    }

    /**
     * 
     *
     * @param playCards    
     * @param prevCardType 
     */
    checkValidCardType(playCards: number[], prevCardType: any)
    {
        this.sort(playCards);
        this.sort(prevCardType.cards);
        
        // 2
        if (this.getCardRank(prevCardType.cards[prevCardType.cards.length - 1]) == CARD_RANK.TWO) {
            // 12
            if (prevCardType.cards.length <= 2) {
                if (playCards.length == 4) {
                    var ret = this.checkFour(playCards[0], playCards[1], playCards[2], playCards[3]);
                    if (ret) {
                        return CARD_TYPE.FOUR;
                    }
                } else if (playCards.length >= (4 + 2 * prevCardType.cards.length)) {
                    var ret = this.checkPairs(playCards);
                    if (ret) {
                        return CARD_TYPE.PAIRS;
                    }
                }
            }
        }
        let cardType:number = this.getCardType(prevCardType.cards);
        // 43
        if (cardType == CARD_TYPE.PAIRS) {
            if (playCards.length == 4) {
                var ret = this.checkFour(playCards[0], playCards[1], playCards[2], playCards[3]);
                if (ret) {
                    return CARD_TYPE.FOUR;
                } else {
                    return CARD_TYPE.NULL;
                }
            }
        }

        // 4
        if (cardType == CARD_TYPE.FOUR) {
            if (playCards.length >= 8) {
                var ret = this.checkPairs(playCards);
                if (ret) {
                    return CARD_TYPE.PAIRS;
                } else {
                    return CARD_TYPE.NULL;
                }
            }
        }
        
        let pn = prevCardType.cards.length;
        if (prevCardType.cards.length == playCards.length) {
            if (pn == 1) {
                var ret = this.greaterThan(playCards[0], prevCardType.cards[0]);
                if (ret) {
                    return CARD_TYPE.SINGLE;
                }
            } else if (pn == 3) {
                if (cardType == CARD_TYPE.THREE) {
                    if (this.checkThree(playCards[0], playCards[1], playCards[2])) {
                        var ret = this.greaterThan(playCards[2], prevCardType.cards[2]);
                        if (ret) {
                            return CARD_TYPE.THREE;
                        }
                    }
                }
            } else if (pn % 2 == 0) {
                if (pn == 2) {
                    if (cardType == CARD_TYPE.PAIR) {
                        if (this.checkPair(playCards[0], playCards[1])) {
                            var ret = this.greaterThan(playCards[1], prevCardType.cards[1]);
                            if (ret) {
                                return CARD_TYPE.PAIR;
                            }
                        }
                    }
                } else if (pn == 4) {
                    if (cardType == CARD_TYPE.FOUR) {
                        if (this.checkFour(playCards[0], playCards[1], playCards[2], playCards[3])) {
                            var ret = this.greaterThan(playCards[3], prevCardType.cards[3]);
                            if (ret) {
                                return CARD_TYPE.FOUR;
                            }
                        }
                    }
                }
            }

            if (pn > 2 && cardType == CARD_TYPE.STRAIGHT) {
                if (playCards.length == pn && this.checkStraight(playCards)) {
                    var ret = this.greaterThan(playCards[pn - 1], prevCardType.cards[pn - 1]);
                    if (ret) {
                        return CARD_TYPE.STRAIGHT;
                    }
                }
            }
        }

        if (pn >= 6) {
            if (cardType == CARD_TYPE.PAIRS) {
                if (this.checkPairs(playCards)) {
                    if (playCards.length > pn
                        || (playCards.length == prevCardType.cards.length && this.greaterThan(playCards[pn - 1], prevCardType.cards[pn - 1]))) {
                        return CARD_TYPE.PAIRS;
                    }
                }
            }
        }

        return CARD_TYPE.NULL;
    }

    /**
     * 
     *
     * @param handCards 
     * @param playCards 
     */
    checkPlayCards(handCards: number[], playCards: number[]) : boolean
    {
        for (let card of playCards) {
            if (handCards.indexOf(card) == -1) {
                return false;
            }
        }
        
        return true;
    }

    /**
     * 
     * @param handCards 
     * @param playCards 
     */
    playCards(handCards: number[], playCards: number[]) : {result:boolean, leftCards: number[]}
    {
        let tempCards: number[] = [...handCards];
        for (let card of playCards) {
            let index = tempCards.indexOf(card);
            if (index == -1) {
                return {result: false, leftCards: handCards};
            } else {
                tempCards.splice(index, 1);
            }
        }

        return {result: true, leftCards: tempCards};
    }

    /**
     * 
     * @param handCards    
     * @param prevCardType 
     */
    findPromptCards(handCards: number[], prevCardType?: PrevCards)
    {
        let ret:any = {
            single: [],
            pair: [],
            three: [],
            straight: [],
            pairs: [],
            four: [],
        };

        if (handCards.length == 0) {
            return ret;
        }

        this.sort(handCards);

        let cardType:number = 0;

        let pn = 0;
        if (prevCardType) {
            this.sort(prevCardType.cards);
            pn = prevCardType.cards.length;
            cardType = this.getCardType(prevCardType.cards);
        }

        // 
        if (!prevCardType || cardType == CARD_TYPE.SINGLE) {
            for (let card of handCards) {
                if (!prevCardType || this.checkValidCardType([card], prevCardType) > CARD_TYPE.NULL) {
                    ret.single.push({
                        type: CARD_TYPE.SINGLE,
                        cards: [card]
                    });
                }
            }
        }

        // 
        if (!prevCardType || cardType == CARD_TYPE.PAIR) {
            for (let i = 0; i < handCards.length - 1; i++) {
                if (this.checkPair(handCards[i], handCards[i + 1])
                    && (!prevCardType || this.checkValidCardType([handCards[i], handCards[i + 1]], prevCardType) > CARD_TYPE.NULL)) {
                    ret.pair.push({
                        type: CARD_TYPE.PAIR,
                        cards: [handCards[i], handCards[i + 1]]
                    })
                }
            }
        }

        // 
        if (!prevCardType || cardType == CARD_TYPE.THREE) {
            for (let i = 0; i < handCards.length - 2; i++) {
                if (this.checkThree(handCards[i], handCards[i + 1], handCards[i + 2])
                    && (!prevCardType || this.checkValidCardType([handCards[i], handCards[i + 1], handCards[i + 2]], prevCardType) > CARD_TYPE.NULL)) {
                    ret.three.push({
                        type: CARD_TYPE.PAIR,
                        cards: [handCards[i], handCards[i + 1], handCards[i + 2]]
                    })
                }
            }
        }

        // 
        if (!prevCardType || cardType == CARD_TYPE.STRAIGHT) {
            if (handCards.length >= pn) {
                let straight_list = [];
                for (let i = handCards.length - 1; i > pn - 2; i--) {
                    if (this.getCardRank(handCards[i]) == CARD_RANK.TWO) {
                        continue;
                    }

                    let straight = [];
                    let flag = true;
                    for (let j = i; j >=0; j--) {
                        if (straight.length == 0) {
                            if (!prevCardType || this.greaterThan(handCards[j], prevCardType.cards[pn - 1]) ) {
                                straight.push(handCards[j]);
                            }
                        } else {
                            if (this.getCardRank(handCards[j]) == this.getCardRank(straight[straight.length -1])) {
                                // 
                                if (straight.length > 1) {
                                    straight[straight.length -1] = handCards[j];
                                }
                            } else if (this.checkContinuous(handCards[j], straight[straight.length -1])) {
                                // 
                                straight.push(handCards[j]);
                            } else {
                                // , 
                                i = j + 1;
                                flag = false;
                            }
                        }

                        if (!flag || j == 0 || (prevCardType && pn == straight.length)) {
                            if (straight.length >= 3 && (!prevCardType || this.checkValidCardType(straight, prevCardType) > CARD_TYPE.NULL)) {
                                straight_list.push({
                                    type: CARD_TYPE.STRAIGHT,
                                    cards: straight.reverse()
                                });
                            }

                            break;
                        }
                    }
                }

                if (straight_list.length > 0) {
                    for (let temp of straight_list.reverse()) {
                        ret.straight.push(temp);
                    }
                }
            }
        }

        // , 32 4223
        if (!prevCardType
            || cardType == CARD_TYPE.PAIRS
            || (cardType == CARD_TYPE.SINGLE && this.getCardRank(prevCardType.cards[0]) == CARD_RANK.TWO)
            || (cardType == CARD_TYPE.PAIR && this.getCardRank(prevCardType.cards[0]) == CARD_RANK.TWO)
            || cardType == CARD_TYPE.FOUR
            ) {
            if (handCards.length >= 6) {
                let pairs_list = [];
                for (let i = handCards.length-1; i >= 5; i--) {
                    let pairs = [];
                    let flag = true;
                    for (let j = i; j >= 0; j--) {
                        if (pairs.length == 0) {
                            pairs.push(handCards[j]);
                        } else {
                            if (pairs.length % 2 == 0) {
                                //   
                                if (this.checkContinuous(handCards[j], pairs[pairs.length - 1])) {
                                    pairs.push(handCards[j]);
                                } else if (this.getCardRank(handCards[j]) == this.getCardRank(pairs[pairs.length -1])) {
                                    pairs[pairs.length -1] = handCards[j];
                                } else {
                                    // 
                                    flag = false;
                                }
                            } else {
                                if (this.getCardRank(handCards[j]) == this.getCardRank(pairs[pairs.length -1])) {
                                    pairs.push(handCards[j]);
                                } else {
                                    // 
                                    flag = false;
                                }
                            }
                        }

                        // 
                        if (!flag || j == 0) {
                            // 
                            if (pairs.length % 2 == 1) {
                                pairs.pop();
                            }

                            let tempPairs = [...pairs];
                            if (pairs.length >= 6 && (!prevCardType || this.checkValidCardType(tempPairs, prevCardType) > CARD_TYPE.NULL)) {
                                pairs_list.push({
                                    type: CARD_TYPE.PAIRS,
                                    cards: pairs
                                });
                            }

                            // 
                            i = j + 1;
                            break;
                        }
                    }
                }

                if (pairs_list.length > 0) {
                    for (let temp of pairs_list.reverse()) {
                        temp.cards.reverse();
                        ret.pairs.push(temp);
                    }
                }
            }
        }

        // , 223
        if (!prevCardType
            || cardType == CARD_TYPE.FOUR
            || (cardType == CARD_TYPE.PAIRS && prevCardType.cards.length == 6)
            || ((cardType == CARD_TYPE.PAIR || cardType == CARD_TYPE.SINGLE) && this.getCardRank(prevCardType.cards[0]) == CARD_RANK.TWO)
        ) {
            if (handCards.length >= 4) {
                for (let i = 0; i < handCards.length - 3; i++) {
                    if (this.checkFour(handCards[i], handCards[i + 1], handCards[i + 2], handCards[i + 3])
                        && (!prevCardType || this.checkValidCardType([handCards[i], handCards[i + 1], handCards[i + 2], handCards[i + 3]], prevCardType) > CARD_TYPE.NULL)) {
                        ret.four.push({
                            type: CARD_TYPE.FOUR,
                            cards: [handCards[i], handCards[i + 1], handCards[i + 2], handCards[i + 3]]
                        })
                    }
                }
            }
        }

        return ret;
    }

    /**
     * 
     * @param handCards    
     * @param prevCardType 
     */
    getPlayCards(handCards: number[], prevCardType?: any) {
        let card_type_list = this.findPromptCards(handCards, prevCardType);

        let {single, pair, three, straight, pairs, four} = card_type_list;

        if (!prevCardType) {
            // 1
            if (this.getCardType(handCards) > CARD_TYPE.NULL) {
                return handCards;
            }

            //  ->  ->  -> 
            let first_card = handCards[0];
            if (straight.length > 0 && straight[0].cards.includes(first_card)) {
                return straight[0].cards;
            } else if (three.length > 0 && three[0].cards.includes(first_card)) {
                return three[0].cards;
            } else if (pair.length > 0 && pair[0].cards.includes(first_card)) {
                return pair[0].cards;
            } else {
                return single[0].cards;
            }
        } else {
            // 1
            if (this.checkValidCardType(handCards, prevCardType) > CARD_TYPE.NULL) {
                return handCards;
            }
            let cardType:number = this.getCardType(prevCardType.cards);

            // 
            if (cardType != CARD_TYPE.PAIRS && cardType != CARD_TYPE.FOUR) {
                switch (cardType) {
                    case CARD_TYPE.SINGLE:
                        if (single.length > 0) {
                            return single[0].cards;
                        }
                        break;
                    case CARD_TYPE.PAIR:
                        if (pair.length > 0) {
                            return pair[0].cards;
                        }
                        break;
                    case CARD_TYPE.STRAIGHT:
                        if (straight.length > 0) {
                            return straight[0].cards;
                        }
                        break;
                    case CARD_TYPE.THREE:
                        if (three.length > 0) {
                            return three[0].cards;
                        }
                        break;
                }
            }

            // 2  
            if (cardType == CARD_TYPE.PAIRS
                || cardType == CARD_TYPE.FOUR
                || (prevCardType.cards.length <= 2 && this.getCardRank(prevCardType.cards[0]) == CARD_RANK.TWO)) {
                if (pairs.length > 0) {
                    let cards = pairs[0].cards;
                    if (cards.length >= 8) {
                        // 
                        if (four.length > 0) {
                            return four[0].cards;
                        }

                        return cards;
                    }
                }

                // 
                if (four.length > 0) {
                    return four[0].cards;
                }
            }
        }

        return [];
    }

    /**
     * 
     * @param handCards    
     * @param prevCardType 
     */
     checkCanPlayCards(handCards: number[], prevCardType?: PrevCards) {
        let card_type_list = this.findPromptCards(handCards, prevCardType);
        let {single, pair, three, straight, pairs, four} = card_type_list;

        if (single.length > 0) {
            return true;
        } else if (pair.length > 0) {
            return true;
        } else if (three.length > 0) {
            return true;
        } else if (straight.length > 0) {
            return true;
        } else if (pairs.length > 0) {
            return true;
        } else if (four.length > 0) {
            return true;
        }

        return false;
    }

    /**
     * 
     * @param handCards 
     */
    getTongPaiCards(handCards: number[]) : number[] {
        let card_type_list = this.findPromptCards(handCards);
        let {single, pair, three, straight, pairs, four} = card_type_list;

        // 3~A
        if (straight.length > 0 && straight[straight.length - 1].cards.length == 12) {
            return straight[straight.length - 1].cards;
        }

        // 35
        if (pairs.length > 0) {
            for (let tempCardType of pairs) {
                if (tempCardType.cards.includes(0x03) || tempCardType.cards.length >= 10) {
                    return tempCardType.cards;
                }
            }
        }

        // 2
        if (four.length > 0) {
            for (let tempCardType of four) {
                if (this.getCardRank(tempCardType.cards[0]) == CARD_RANK.TWO) {
                    return tempCardType.cards;
                }
            }
        }

        // 6
        if (pair.length >= 6) {
            let sixPairCards:number[] = [];
            for (let tempCardType of pair) {
                let isIn = false;
                for (let tempCard of tempCardType.cards) {
                    if (sixPairCards.indexOf(tempCard) != -1) {
                        isIn = true;
                        continue;
                    }
                }

                if (!isIn) {
                    sixPairCards.push(...tempCardType.cards);
                }
            }

            if (sixPairCards.length >= 12) {
                return sixPairCards;
            }
        }

        let blackCards:number[] = [];
        let redCards:number[] = [];
        let colors = [blackCards, redCards];
        for (let card of handCards) {
            colors[this.getCardColor(card)].push(card);
        }

        for (let color of colors) {
            if (color.length >= 12) {
                return color;
            }
        }

        return [];
    }

    /**
     * 
     * @param handCards 
     */
    calcHandCardTimes(handCards: number[], cfg: any) {
        // 2
        let timeList = [];
        let copyCards = [...handCards];

        if (handCards.indexOf(0x02) != -1) { //2
            timeList.push({cards: [0x02], times: cfg.spade2});
            this.playCards(copyCards, [0x02]);
        }

        if (handCards.indexOf(0x12) != -1) { //2
            timeList.push({cards: [0x02], times: cfg.club2});
            this.playCards(copyCards, [0x12]);
        }

        if (handCards.indexOf(0x22) != -1) { //2
            timeList.push({cards: [0x02], times: cfg.diamond2});
            this.playCards(copyCards, [0x22]);
        }

        if (handCards.indexOf(0x32) != -1) { //2
            timeList.push({cards: [0x02], times: cfg.heart2});
            this.playCards(copyCards, [0x32]);
        }

        for (let i = 0; i < handCards.length; i++) {
            let cardTypeList = this.findPromptCards(copyCards);
            let {pairs, four} = cardTypeList;

            if (pairs.length > 0 && pairs[pairs.length - 1].cards.length >= 8) {
                timeList.push({cards: pairs[pairs.length - 1].cards, times: cfg.pairs4});
                this.playCards(copyCards, pairs[pairs.length - 1].cards);
                continue;
            }

            if (four.length > 0) {
                for (let tempCardType of four) {
                    timeList.push({cards: tempCardType.cards, times: cfg.four});
                    this.playCards(copyCards, tempCardType.cards);
                }
                continue;
            }

            if (pairs.length > 0) {
                timeList.push({cards: pairs[0].cards, times: cfg.pairs3});
                this.playCards(copyCards, pairs[0].cards);
                continue;
            }

            break;
        }

        let badTotalTimes = 0;
        for (let t of timeList) {
            badTotalTimes += t.times;
        }

        return {badTotalTimes, timeList, leftCards: copyCards};
    }
}
