import { _decorator, Component, Node, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;

import PDK_Game from "../PDK_Game";
import { FaceConfigContainer, FaceData } from '../Conifg/FaceConfigContainer';
import { find } from 'cc';
import { PokerManager, UserInfo } from 'db://assets/module_basic/scripts/poker/PokerManager';
import PDK_Player from '../PDK_Player';
import PDK_PlayerSoundHelper from './PDK_PlayerSoundHelper';
import { DuanjuConfigContainer, DuanjuData } from '../Conifg/DuanjuConfigContainer';
import { ConfigManager } from '../ConfigManager';
import { LanguageConfigContainer } from '../Conifg/LanguageConfigContainer';
import { Animation } from 'cc';
import PDK_EmojiComp from './PDK_EmojiComp';
import GameTools from '../../kernel/GameTools';
import { assetManager } from 'cc';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { PokerUtils } from '../message/PokerUtils';
import { director } from 'cc';
import { EventMessage } from '../message/Define';
import { MsgChat } from 'db://assets/module_basic/shared/protocols/public/chat/MsgChat';

@ccclass('PDKChatHelper')
export default class PDK_ChatHelper extends Component {
    gameScript: PDK_Game = null;
    start() {
        this.gameScript = find("Canvas").getComponent(PDK_Game);
        this.bindEvent();
    }
    bindEvent() {
        director.on(EventMessage.POKER_USER_CHAT,this.onNewChatMsg,this);
    }
    onDestroy() {
        director.off(EventMessage.POKER_USER_CHAT,this.onNewChatMsg,this);
    }
    onNewChatMsg(data:MsgChat) {
        var userInfo:UserInfo = PokerManager.inst.getUserInfoByID(data.user.uid);
        if(!userInfo) return;


        let seatID = PokerUtils.getRelativeSeatID(userInfo.chairID, PokerManager.inst.myChairID);
        let chairData: UserInfo = PokerManager.inst.getTableUserItem(userInfo.chairID);
        if (chairData == null) return;
        let text: string = null;
        if (data.content.indexOf("chat_face_") != -1) {
            // 
            let id: number = Number(data.content.replace("chat_face_", ""));
            let faceConfig: FaceConfigContainer = ConfigManager.getInstance().getConfig(FaceConfigContainer);
            if (!isNaN(id) && faceConfig.getFaceDataById(id) != null) {
                let faceData: FaceData = faceConfig.getFaceDataById(id);
                this.showEmoji(this.gameScript.playerScript[seatID].chatNode, faceData);
                PDK_PlayerSoundHelper.playerSound(faceData.sound, 1);
                return;
            }
        }
            let langConfig = ConfigManager.getInstance().getConfig(LanguageConfigContainer) as LanguageConfigContainer;
            if (data.content.indexOf("duanju_") != -1) {
            // 
                let id: number = Number(data.content.replace("duanju_", ""));
                let duanjuConfig: DuanjuConfigContainer = ConfigManager.getInstance().getConfig(DuanjuConfigContainer);
                if (!isNaN(id) && duanjuConfig.getDataById(id) != null) {
                    let duanjuData: DuanjuData = duanjuConfig.getDataById(id);
                    text = langConfig.getLang(duanjuData.text);
                    PDK_PlayerSoundHelper.playerSound(duanjuData.sound, 1);
                }
            }

        if (text == null) {
            text = langConfig.getLang(data.content);
        }

        console.log("", text);
        let player: PDK_Player = this.gameScript.playerScript[seatID];
        if (player != null) {
            player.chatText.string = text;
            player.chatNode.getComponent(Animation).play();
        }
    }
    private showEmoji(chatNode: Node, faceData: FaceData) {
        let faceNode: Node = chatNode.getChildByName("chat_face");
        let bundle = assetManager.getBundle(ModuleDef.POKER_PDK_YUENAN);
        bundle.loadDir<SpriteFrame>("images/" + faceData.res + "/", SpriteFrame, (err, assets: SpriteFrame[]) => {
            if(err == null)
            {
                this.onLoadEmoji(assets, faceNode, faceData);
            }
            
        });
    }
    private onLoadEmoji(assets: SpriteFrame[], faceNode: Node, faceData: FaceData) {
        if (assets == null) return;
        let emoji: PDK_EmojiComp = faceNode.getComponent(PDK_EmojiComp);
        if (emoji == null) {
            emoji = faceNode.addComponent(PDK_EmojiComp);
        }
        emoji.startFrameAnimation(assets);
    }
}
