import { _decorator, Component, Sprite, Node, Label, SpriteAtlas } from 'cc';
import { find } from 'cc';
import { CARD_RANK } from './message/Define';
import { PokerUtils } from './message/PokerUtils';
const { ccclass, property } = _decorator;

@ccclass('PDKCard')
export default class PDK_Card extends Component {
    
//    //by_009:
    @property(Sprite)
    value: Sprite | null = null;
//    //by_009:()
    @property(Sprite)
    color_mini: Sprite | null = null;
//    //by_009:()
    @property(Sprite)
    color_big: Sprite | null = null;
//    //by_009:
    @property(Node)
    daWang: Node | null = null;
//    //by_009:
    @property(Sprite)
    daWangValue: Sprite | null = null;
//    //by_009:
    @property(Node)
    teamNode: Node | null = null;
//    //by_009:
    @property(Node)
    jiaoBiao: Node | null = null;
//    //by_009:
    @property(Label)
    count: Label | null = null;
//    //by_009:
    @property(Node)
    bm: Node | null = null;
//    //by_009:
    @property(SpriteAtlas)
    paiAtlas: SpriteAtlas | null = null;
//    //by_009:
    @property(Node)
    select: Node | null = null;
//    // LIFE-CYCLE CALLBACKS:
    private m_isSelected: boolean = false;

    _cardID = 0;
    _team = 0;
    
//    //by_009:
    _jiaoBiaoValue = 0;

    get cardID() {
        return this._cardID;
    }
    set cardID(value) {
        this._cardID = value;
        this.setCard(this._cardID);
    }
    @property
    get team() {
        return this._team;
    }
    set team(value) {
        this._team = value;
        this.setTeam(value);
    }
    get jiaoBiaoValue() {
        return this._jiaoBiaoValue;
    }
    set jiaoBiaoValue(value) {
        this._jiaoBiaoValue = value;
        this.setjiaoBiaoValue(value);
    }

//    // 
    get isSelected(): boolean {
        return this.m_isSelected;
    }
    set isSelected(value: boolean) {
        this.m_isSelected = value;
    }
    onLoad() { }
    start() { }
//    //by_009:
    private setCard(value:number) {
        if (value == null || value == 0) 
        {
            this.bm.active = true;
            return;
        } else 
        {
            this.bm.active = false;
        }
        var cardRank: number = PokerUtils.getCardRank(value);
        var cardColor: number = PDK_Card.getCardColor(value);
        if (cardRank == CARD_RANK.JokerSmall || cardRank == CARD_RANK.JokerBig) 
        {//
            this.value.node.active = false;
            this.color_mini.node.active = false;
            this.color_big.node.active = false;

            this.daWang.active = true;
            this.daWangValue.spriteFrame = this.paiAtlas.getSpriteFrame("value_" + cardRank);
        } else {
            this.value.node.active = true;
            this.color_mini.node.active = true;
            this.color_big.node.active = true;
            this.daWang.active = false;
            
            if (value >= 17 && value <= 29) {
                // 
                this.value.spriteFrame = this.paiAtlas.getSpriteFrame(`${cardRank}_1`);
            }
            else if (value >= 49 && value <= 61) {
                // 
                this.value.spriteFrame = this.paiAtlas.getSpriteFrame(`${cardRank}_0`);
            }
            else {
                this.value.spriteFrame = this.paiAtlas.getSpriteFrame(`${cardRank}_${cardColor % 2}`);
            }

            this.color_mini.spriteFrame = this.paiAtlas.getSpriteFrame("mini_" + cardColor);
            switch (cardRank) {
                case 11:
                case 12:
                case 13:
                case 27:
                case 28:
                case 29:
                case 43:
                case 44:
                case 45:
                case 59:
                case 60:
                case 61:
                    this.color_big.spriteFrame = this.paiAtlas.getSpriteFrame("big_" + this._cardID);
                    break;
                default:
                    this.color_big.spriteFrame = this.paiAtlas.getSpriteFrame("big_" + cardColor);
                break;
            }


        }
    }
   //by_009:
    setTeam(value = 0) {
        if (value == 0) {
            this.teamNode.children[0].active = false;
            this.teamNode.children[1].active = false;
        } else if (value == 1) {
            this.teamNode.children[0].active = true;
            this.teamNode.children[1].active = false;
        } else if (value == 2) {
            this.teamNode.children[0].active = false;
            this.teamNode.children[1].active = true;
        }
    }
   //by_009:
    setjiaoBiaoValue(value) {
        if (value == null || value == 0)
        {
            this.jiaoBiao.active = false;
            return;
        }
        this.jiaoBiao.active = true;
        find("value", this.jiaoBiao).getComponent(Label).string = value + "";
    }
    static getCardColor(cardData) {
        if (cardData <= 13) {
            // 
            return 3;
        }
        else if (cardData >= 17 && cardData <= 29) {
            // 
            return 2;
        }
        else if (cardData >= 33 && cardData <= 45) {
            // 
            return 0;
        }
        else if (cardData >= 49 && cardData <= 61) {
            // 
            return 1;
        }
        return (cardData & 0xF0) >> 4;
    }
}