
import { _decorator, Component, Label, Node, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

import PDK_Card from "./PDK_Card";
import PDK_Game from "./PDK_Game";
import PDK_HeadframeAni from "./misc/PDK_HeadframeAni";
import PDK_PlayerInfo from "./misc/PDK_PlayerInfo";
import PDK_CardTitle from "./misc/PDK_CardTitle";
import { CARD_TYPE } from "./PDK_ConstDef";
import PDK_PlayerSoundHelper from "./misc/PDK_PlayerSoundHelper";
import { PokerManager, UserInfo} from '../../module_basic/scripts/poker/PokerManager';
import GameTools from '../kernel/GameTools';
import { Tween } from 'cc';
import { tween } from 'cc';
import { easing } from 'cc';
import { instantiate } from 'cc';
import { UITransform } from 'cc';
import OnGameSyncPlayerGold from '../kernel/OnGameSyncPlayerGold';
import { CARD_RANK } from './message/Define';
import { UserResultData } from './message/GameManager';
import { PokerUtils } from './message/PokerUtils';
import { IUserData } from '../../module_basic/shared/types/RoomData';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { UserMgr } from '../../module_basic/scripts/UserMgr';
import { Sprite } from 'cc';

@ccclass('PDKPlayer')
export default class PDK_Player extends Component {
    @property(Label)
    nickname: Label | null = null;
    @property(Label)
    id: Label | null = null;
    @property(Node)
    icon_bank: Node | null = null;
    @property(Node)
    shouPai: Node | null = null;
    @property(Node)
    chuPai: Node | null = null;
    @property(Node)
    cardCount: Node | null = null;
    @property(Node)
    clockPos: Node | null = null;
    @property(Node)
    ready: Node | null = null;
    @property(Node)
    head: Node | null = null;
    @property(Label)
    score: Label | null = null;
    @property(PDK_HeadframeAni)
    headAni: PDK_HeadframeAni = null;
    @property(Node)
    chatNode: Node | null = null;
    @property(Label)
    chatText: Label | null = null;

    userId: string = "";
    private m_isBind: boolean = false;
    chariID: number;
    private m_cardTitle: PDK_CardTitle = null;
    private m_scoreNum: number = 0;
    private m_soundHelper: PDK_PlayerSoundHelper = new PDK_PlayerSoundHelper();
    onLoad() {
        this.node.active = false;
        this.id.string = "";
        this.shouPai?.destroyAllChildren();
        this.chuPai.destroyAllChildren();
        this.setCardCount(0, false);
        this.setReady(false);
        this.score && (this.score.string = "0");
        let chatBubble = this.chatNode.getChildByName("chat_bubble");
        chatBubble.scale = new Vec3(0,0);
        this.m_cardTitle = this.node.getChildByName("card_title").getComponent(PDK_CardTitle);
    }
    start() { }
    onDestroy() {
        this.m_soundHelper = null;
    }
   /**
   * 
   */
    initPlayerInfo(userItem: UserInfo) {
        this.node.active = false;
        this.reset();
        if (userItem) {
            this.node.active = true;
            this.nickname && (this.nickname.string = userItem.nickname);

            // this.head && (GameTools.loadWxHead(this.head, userItem.head));
            // if (!this.m_isBind)
            // {p
            //     this.m_isBind = true;
            //     this.head && (this.head.on(cc.Node.EventType.TOUCH_END, this.onClickHead, this));
            //  }
           // this.setOffline(userItem.online == 0);

            var roomPlayerData:IUserData = RoomMgr.inst.getPlayData(userItem.userID);
            if(roomPlayerData)
            {
                UserMgr.inst.loadHeadIcon(roomPlayerData.visualId,this.head.getComponent(Sprite));
            }

            this.setBanker(false);
            this.setReady(false);

            this.chariID = userItem.chairID;
            this.userId = userItem.userID;
            if (userItem.isReady == false){
                this.setReady(true)
            }

            // 
            this.setScore(userItem.gold);
            let seatID = PokerUtils.getRelativeSeatID(userItem.chairID, PokerManager.inst.myChairID);
            if (this.m_soundHelper != null) {
                this.m_soundHelper.init(seatID, 1); // todo
            }
        }
    }
    setCardCount(num: number, isActive = true) {
        if (this.cardCount.active != isActive) {
            this.cardCount.active = isActive;
        }
        let lbl = this.cardCount.getChildByName("label").getComponent(Label);
        if (lbl != null) {
            lbl.string = num.toString();
        }
    }
//    //
    leftCardCount(reduceNum: number) {
        this.setCardCount(reduceNum);
       // let node = cc.find("label", this.cardCount);
       // if (node) {
       //     let labelCom = node.getComponent(cc.Label);
       //     let curNum = Number(labelCom.string)
       //     if (curNum) {
       //         this.cardCount.active = true;
       //         labelCom.string = reduceNum + '';
       //     }
       // }
    }
    fillShouPai(arrCardId: number[]) {
        if (this.shouPai) {
            for (let i = 0; i < this.shouPai.children.length; i++) {
                if (i < arrCardId.length) {
                    let element = this.shouPai.children[i];
                    let cardId = arrCardId[i];
                    if (element != null) {
                        element.getComponent(PDK_Card).cardID = cardId;
                    }
                }
            }
        }
    }
    playChuPai(cards: number[], isGray: boolean = false) {
        // console.log("chu pai : " + this.chariID);
        let isMatch: boolean = true;
        if (this.chuPai) {
            if (this.chuPai.children.length !== cards.length) {
                isMatch = false;
            } else {
                for (let i = 0; i < this.chuPai.children.length; i++) {
                    let element = this.chuPai.children[i];
                    let cardComp = element.getComponent(PDK_Card);
                    if (cardComp && cardComp.cardID !== cards[i]) {
                        isMatch = false;
                        break;
                    }
                }
            }
        }

        if (isMatch) {
            return;
        }

        if (this.chuPai) {
            this.chuPai.destroyAllChildren();
        }

        let delay = 0;
        for (let i = 0; i < cards.length; i++) {
            let card = cards[i];
            if (this.chuPai) {
                let cardTemp = instantiate(PDK_Game.ins.cardPrefab);
                cardTemp.setParent(this.chuPai);
                cardTemp.setPosition(Vec3.ZERO);
                let cardComp = cardTemp.getComponent(PDK_Card);
                if (cardComp) {
                    cardComp.cardID = card;
                }
               //  UITransform 
                const uiTransform = cardTemp.getComponent(UITransform);
                if (uiTransform) {
                    uiTransform.anchorX = 0;
                    uiTransform.anchorY = 0.5;
                }

                if (isGray) {
                    if (cardComp) {
                        cardComp.select.active = true;
                    }
                }

                cardTemp.opacity = 0;
                let cardChild = cardTemp.getChildByName("card");
                if (cardChild) {
                    cardChild.setScale(Vec3.ZERO);
                }

                // 
                const fadeInTween = tween(cardTemp)
                   .to(0.2, { opacity: 255 });

                // 
                const scaleTween = cardChild ? tween(cardChild)
                   .to(0.2, { scale: new Vec3(1, 1, 1) }, { easing: easing.backOut }) : null;

                setTimeout(() => {
                    if (cardTemp.isValid) {
                        fadeInTween.start();
                        scaleTween?.start();
                    }
                }, delay * 1000);

                delay += 0.05;
            }

            if (this.shouPai) {
                for (let j = 0; j < this.shouPai.children.length; j++) {
                    let element = this.shouPai.children[j];
                    let shouPaiCardComp = element.getComponent(PDK_Card);
                    if (shouPaiCardComp && shouPaiCardComp.cardID === card) {
                        element.destroy();
                        break;
                    }
                }
            }
        }
    }
    
    showCardTitle(cards: number[], type: number) {
        if (type == CARD_TYPE.NULL || this.m_cardTitle == null) return;

        if (type == CARD_TYPE.PAIR) {
            let card = cards[0];
            if (card && PokerUtils.getCardRank(card) == CARD_RANK.TWO) {
                // 2
                this.m_cardTitle.showShuang2();
                return;
            }
        }
        if (type == CARD_TYPE.PAIRS) {
            if (cards.length == 6) {
                this.m_cardTitle.showLian3Dui();
                return;
            }
            if (cards.length == 8) {
                this.m_cardTitle.showLian4Dui();
                return;
            }
        }

        if (type == CARD_TYPE.FOUR) {
           // 
            this.m_cardTitle.show4ji();
            return;
        }
    }
    resetChuPai(isAni: boolean = false) {
        if (this.chuPai) {
            if (isAni) {
                let delay = 0;
                const children = this.chuPai.children;
                for (let i = children.length - 1; i >= 0; i--) {
                    const element = children[i];
                    //  tween 
                    Tween.stopAllByTarget(element);

                    const fadeOutTween = tween(element)
                       .to(0.06, { opacity: 0 })
                       .call(() => {
                            if (element.isValid) {
                                element.removeFromParent();
                                element.destroy();
                            }
                        });

                    setTimeout(() => {
                        if (element.isValid) {
                            fadeOutTween.start();
                        }
                    }, delay * 1000);

                    delay += 0.03;
                }
            } else {
                const children = this.chuPai.children;
                for (let i = children.length - 1; i >= 0; i--) {
                    const element = children[i];
                    //  tween 
                    Tween.stopAllByTarget(element);

                    tween(element)
                       .to(0.1, { opacity: 0 })
                       .call(() => {
                            if (element.isValid) {
                                element.removeFromParent();
                                element.destroy();
                            }
                        })
                       .start();
                }
            }
        }
        this.m_cardTitle?.ready(); // 
    }
    resetShouPai() {
        this.shouPai?.destroyAllChildren();
    }
    setReady(isReady = true) {
        if (this.ready) {
            if (isReady) {
                this.ready.active = true;
                this.ready.opacity = 0;
                this.ready.setScale(Vec3.ZERO);

                //  tween 
                Tween.stopAllByTarget(this.ready);

                // 
                const fadeInTween = tween(this.ready)
                   .to(0.1, { opacity: 255 });

                // 
                const scaleTween = tween(this.ready)
                   .to(0.2, { scale: new Vec3(1, 1, 1) }, { easing: easing.backOut });

                // 
                tween(this.ready)
                   .parallel(fadeInTween, scaleTween)
                   .start();
            } else {
                this.ready.active = false;
            }
        }
    }
    setBanker(isBanker: boolean) {
        this.icon_bank && (this.icon_bank.active = !!isBanker)
    }
    reset() {
        this.shouPai?.destroyAllChildren();
        this.chuPai.destroyAllChildren();
        this.setCardCount(0, false);
        this.headAni.reset();
    }
   /**
    * 
    * @param score 
    * @returns 
    */
    setScore(score): number {
        this.score && (this.score.string = GameTools.nFormatter(score));
        let offset = score - this.m_scoreNum;
        this.m_scoreNum = score;
        return offset;
    }
    private setScoreLayout() {
        let parent = this.score.node.parent;
        if (parent != null) {
            let goldIcon = parent.getChildByName("icon_gold");
            goldIcon && (goldIcon.active = false)
        }
        this.score && (this.score.node.setPosition(0, 2));
    }
    syncPlayerData(playerData: OnGameSyncPlayerGold) {
        if (playerData.data != null) {
            playerData.data.gold && (this.setScore(playerData.data.gold));
        }
    }
    showHeadWinLoseAni(data: UserResultData) {
        let score = data.winGold - data.bombGold;
        if (data.isWinner) {
            this.headAni.showWin(score);
        }
        else {
            this.headAni.showLose(score);
        }
    }
    showHeadScoreAni(score: number) {
        this.headAni.showScoreAni(score);
    }
    showRoundEndScore(data: UserResultData) {
        this.setScore(data.allGold);
    }
    private onClickHead() {
        // AudioControl.playClick();
        // if (this.userId != KernelData.userID) {
        // GD.GameTool.createPanel("pdk/pdk_player_info", (newNode: cc.Node) => {
        // newNode.getComponent(PDK_PlayerInfo).setData(this.userId, this.chariID)
        // });
        // }
    }
    public showGoldFly(winner: number) {
        this.headAni.showFlyGold(winner);
    }
    public playCardSound(data) {
        if (this.m_soundHelper) {
            this.m_soundHelper.playCardSound(data);
        }
    }
    public playPass() {
        if (this.m_soundHelper) {
            this.m_soundHelper.playPass();
        }
    }
    public playWin() {
        if (this.m_soundHelper) {
            this.m_soundHelper.playWin();
        }
    }
    public playTongpai() {
        if (this.m_soundHelper) {
            this.m_soundHelper.playTongpai();
            this.scheduleOnce(() => {
                this.m_soundHelper.playWin();
            }, 1);
        }
    }
    public showPass(isPass) {
        this.headAni.showPass(isPass);
    }
}
