/**
* 
*/
import { tween } from 'cc';
import { Tween } from 'cc';
import { _decorator, Component, Label } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('UILoading')
export class UILoading extends Component {
    @property(Label)
    public lbl_text = null;

    onLoad () {
        // this.node.active = false 
    }
    onDestroy(): void {
        
    }
    showPanel (show: boolean, text: string, stayTime: number = 5) {
        text = text || "" 
        this.node.active = !!show 
        this.lbl_text.string = text;
        if (show) {
            //  tween 
            tween(this.node)
               .delay(stayTime)
               .call(() => {
                    this.node.active = false;
                })
               .start();
        }
    }

}
