import { _decorator, Component, Node, EditBox, Label } from 'cc';
const { ccclass, property } = _decorator;

import PDK_Game from "../PDK_Game";
import { ConfigManager } from '../ConfigManager';
import { LanguageConfigContainer } from '../Conifg/LanguageConfigContainer';
import { AudioControl } from '../AudioControl';
import { DuanjuConfigContainer, DuanjuData } from '../Conifg/DuanjuConfigContainer';
import { instantiate } from 'cc';
import { FaceConfigContainer, FaceData } from '../Conifg/FaceConfigContainer';
import { find } from 'cc';
import GameTools from '../../kernel/GameTools';
import { GameManager } from '../message/GameManager';

@ccclass('PDKChatMenu')
export default class PDK_ChatMenu extends Component {
//    //by_009:
    @property(Node)
    menu: Node | null = null;
//    //by_009:
    @property(Node)
    content: Node | null = null;
//    // item
    @property(Node)
    phraseItem: Node | null = null;
    @property(Node)
    faceItem: Node | null = null;
//    //by_009:
    @property(Node)
    phraseNode: Node | null = null;
//    //by_009:
    @property(Node)
    faceNode: Node | null = null;
//    //by_009:
    @property(EditBox)
    editbox: EditBox | null = null;
//    //by_009:
    @property(Node)
    btnSend: Node | null = null;
    @property(Label)
    lblSend: Label | null = null;
    @property(Label)
    label_tip: Label | null = null;
//    // LIFE-CYCLE CALLBACKS:
    onLoad() {
        this.faceItem.active = false;
        this.phraseItem.active = false;
    }
    start() {

        this.createDuanju();
        this.createEmoji();

        this.btnSend.on(Node.EventType.TOUCH_END, this.onBtnSend, this);

        let langConfig = ConfigManager.getInstance().getConfig(LanguageConfigContainer);
        this.lblSend.string = langConfig.getLangById(10226);

        this.editbox.placeholder = langConfig.getLangById(10747);
    }
    onTextChange(){
        this.label_tip.string = this.editbox.string.length + "/40"
    }
   //by_009:
    click_phrase(event) {
        AudioControl.playClick();
        this.menu.active = false;
        let index: number = event.currentTarget['duanjuIndex'];
        GameManager.inst.chat(index.toString());
        this.closeMe();
    }
   //by_009:
    click_face(event) {
        AudioControl.playClick();
        this.menu.active = false;
        let txt = event.currentTarget.faceIndex;
        GameManager.inst.chat(txt);
        this.closeMe();
    }
    onBtnSend(event) {
        AudioControl.playClick();
         GameManager.inst.chat(this.editbox.string);
        this.editbox.string = "";
        this.closeMe();
    }
    closeMe() {
        this.node && this.node.destroy();
    }
    private createDuanju() {
        let langConfig = ConfigManager.getInstance().getConfig(LanguageConfigContainer);
        let duanjuArray: DuanjuData[] = ConfigManager.getInstance().getConfig(DuanjuConfigContainer).getDataArray();
        for (let i = 0; i < duanjuArray.length; i++) {
            let duanjuConfig: DuanjuData = duanjuArray[i];
            let langId = duanjuConfig.text;
            let item = instantiate(this.phraseItem);
            item.active = true;
            item.setParent(this.phraseNode);
            let lbl = item.getChildByName("text").getComponent(Label);
            lbl.string = langConfig.getLang(langId);
            item['duanjuIndex'] = "duanju_" + duanjuConfig.id;

            // 
            item.on(Node.EventType.TOUCH_END, this.click_phrase, this);
        }
    }
    private createEmoji() {
        let faceArray: FaceData[] = ConfigManager.getInstance().getConfig(FaceConfigContainer).getDataArray();
        for (let i = 0; i < faceArray.length; i++) {
            let data: FaceData = faceArray[i];
            if (!data.isShow) continue;
            let item = instantiate(this.faceItem);
            let icon = find("icon",item);
            item.active = true;
            item.setParent(this.faceNode);
            item['faceIndex'] = "chat_face_" + data.id;
            GameTools.readLocalImg("images/" + data.icon, icon);
            // 
            item.on(Node.EventType.TOUCH_END, this.click_face, this);
        }
    }
   // update (dt) {}
}
